/**
 * @class Cmp.Contaner
 * @extend Cmp.Widget
 * 
 * <p>
 * 
 * 
 * 
 * 
 * <p>
 * 
 * @cfg {String|Layout} layout ()Cmp.require
 * @cfg {Object} layoutConfig ()layout
 * @cfg {Widget|Object|Array} items (){Widget}Cmp.create{Widget}
 * </p>
 * 
 * @version 2.0.0
 * @since 2016-03-31
 * @author Jinhai
 */
(function(){

	Cmp.define('Cmp.Contaner',{
		extend : 'Cmp.Widget',
		requires : [
			'Cmp.layout.Layout'
		],
		factory : function(ext, reqs){
			var superClazz = ext.prototype,
				layoutClazz = reqs[0];
			
			return Cmp.extend(ext, {
				/**
				 * @public
				 * ;undefined;
				 * @param {Number} index
				 * @return {Widget}
				 */
				getItemAt : function(index){
					var me = this;
					if(me.layoutReady){
						return me.getLayout().getItemAt(index);
					}
					else{
						return undefined;
					}
				},
				/**
				 * ;
				 * 
				 */
				getItemCount : function(){
					var me = this;
					if(me.layoutReady){
						return me.getLayout().getItemCount();
					}
					else{
						return isA(me.items) ? me.items.length : 0;
					}
				},
				/**
				 * @public
				 * 
				 * @param {Widget/Object} item 
				 * @return {Contaner} this
				 */
				addItem : function(item){
					var me = this;
					if(me.layoutReady){
						return me.getLayout().appendItem(item);
					}
					else{
						if(isA(me.items)){
							me.items.push(item);
						}
						else if(isO(me.items)){
							me.items = [me.items, item];
						}
						else{
							me.items = [item];
						}
					}
				},
				/**
				 * @public
				 * 
				 * @param {Number} index 
				 * @return {Boolean} true
				 */
				removeItemAt : function(index){
					return this.getLayout().removeItem(index);	
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,
						ly = me.layout;
					superClazz.initComponent.call(me);
					me.layoutReady = false;
					//
					if(isS(ly)){
						//
						Cmp.require(ly, function(clz){
							if(isF(clz)){
								var lyo = new clz(me.layoutConfig || {});
								if(isF(lyo.layout)){
									me.layout = lyo;
									me.layoutReady = true;
									me.fireLayoutReady();
								}
								else{
									putLog(','+JSON.stringify(ly));
									me.layout = new layoutClazz(me.layoutConfig || {});
									me.layoutReady = true;
									me.fireLayoutReady();
								}
							}
							else{
								putLog(','+ly);
								me.layout = new layoutClazz(me.layoutConfig || {});
								me.layoutReady = true;
								me.fireLayoutReady();
							}
						});
					}
					else if(isO(ly)){
						if(isF(ly.layout)){
							me.layout = ly;
							me.layoutReady = true;
							me.fireLayoutReady();
						}
						else{
							Cmp.create(ly, function(lyo){
								if(isF(lyo.layout)){
									me.layout = lyo;
									me.layoutReady = true;
									me.fireLayoutReady();
								}
								else{
									putLog(','+JSON.stringify(ly));
									me.layout = new layoutClazz(me.layoutConfig || {});
									me.layoutReady = true;
									me.fireLayoutReady();
								}
							});
						}
					}
				},
				/**
				 * Element
				 */
				getLayoutTarget : function(){
					return this.el;
				},
				/**
				 * 
				 * @return {Layout} 
				 */
				getLayout : function(){
					return this.layout;
				},
				/**
				 * @private
				 * @rewrite
				 */
				doRender : function(){
					var me = this;
					superClazz.doRender.call(me);
					if(me.layoutReady){
						me.doLayout();
					}
					else{
						me.onReadyCreateLayout(me.doLayout, me);
					}
				},
				/**
				 * @private
				 */
				doLayout : function(){
					var me = this;
					me.getLayout().layout(me);
				},
				/**
				 * @private
				 */	
				onReadyCreateLayout : function(fn, scope){
					var me = this;
					if(me.layoutReady){
						Cmp.invoke(fn, scope);
					}
					else{
						if(!me.readLayoutListeners){
							me.readLayoutListeners = [];
						}
						me.readLayoutListeners.push({
							fn : fn,
							scope : scope
						});
					}
				}, 
				/**
				 * @private
				 */
				fireLayoutReady : function(){
					var me = this,
						i = 0, o,
						len = isA(me.readLayoutListeners) ? me.readLayoutListeners.length : 0;
					for(;i<len;i++){
						o = me.readLayoutListeners[i];
						Cmp.invoke(o.fn, o.scope);
					}	
				}
			});	
		}
	});
}());