/**
 * 
 *
 * @abstract
 * @class Cmp.grid.SelectionModel
 * @extend Object
 * 
 * @version 2.0.0
 * @since 2016-03-31
 * @author Jinhai
 */
(function(){

	var SelectionModel = function(cfg){
		var me = this;
		if(cfg){
			Cmp.apply(me, cfg);
		}
		me.inited = false;
	}
	SelectionModel.prototype = {
		/**
		 * 
		 * @param {GridPanel} grid 
		 */
		initStyle : function(grid){
			var me = this;
			me.grid = grid;
			grid.addEvents(
				/**
				 * @event
				 * 
				 * @param {SelectionModel} this
				 */
				'selchanged'
			);
		},
		/**
		 * true
		 * 
		 */
		isAllowedSelect : function(){
			return !this.noselect;
		},
		/**
		 * 
		 */
		getCount : function(){
			var me = this;
			if(me.noselect){
				return 0;
			}
			return isA(me.selections) ? me.selections.length : 0;
		},
		/**
		 * 
		 *setSelections
		 */
		clearSelection : function(){
			this.setSelections();
		},
		/**
		 * 
		 * @param {Array} sels ,
		 */
		setSelections : function(sels){
			var me = this;
			if(me.noselect){
				return ;	
			}
			me.selections = sels;
			me.fireEvent('selchanged', me, me.grid);
		},
		/**
		 * 
		 * @return {Array} ,
		 */
		getSelections : function(){
			var me = this;
			if(me.noselect){
				return [];	
			}
			return me.selections;
		},
		/**
		 * 
		 * @return {Object} ,
		 */
		getSelected : function(){
			var me = this;
			if(me.noselect){
				return undefined;	
			}
			return isA(me.selections) && me.selections.length > 0 ?
				me.selections[me.selections.length-1] : undefined;
		},
		/**
		 * 
		 * @param {Row} row 
		 */
		bindRowEvent : Cmp.emptyFn, 
		/**
		 * 
		 * @param {Row} row 
		 */
		releaseRowEvent : Cmp.emptyFn,
		/**
		 * 
		 *
		 * @param {Element} cell 
		 * @param {Object} value 
		 * @param {Record} record 
		 * @param {Number} rowIndex 
		 * @param {Number} colIndex 
		 */ 
		bindCellEvent : Cmp.emptyFn, 
		/**
		 * 
		 * @param {Element} cell 
		 * @param {Number} rowIndex 
		 * @param {Number} colIndex 
		 */ 
		releaseCellEvent : Cmp.emptyFn,
		/**
		 * @private
		 */
		fireEvent : function(){
			var grid = this.grid;
			if(grid){
				grid.fireEvent.apply(grid, arguments);
			}
		}
	};
	Cmp.define('Cmp.grid.SelectionModel',{
		factory : function(){
			return SelectionModel;
		}
	});
}());