/**
 * @class Cmp.flow.FlowItem
 * @extend Cmp.Widget
 * 
 * 
 * <p>
 * 
 * @cfg {String} label ()
 * @cfg {String} anchor ()1<i class="fa fa-checked"></i> HTML
 * @cfg {Boolean} allowedClick ()true
 * @cfg {Function} handler ()allowedClicktrue'enabled' 
 * @cfg {Object} scope ()handlerthis
 * @cfg {String} state ()'active'|'disabled'|'enabled'; :'enabled'
 * </p>
 * 
 * @version 2.3.0
 * @since 2016-06-10
 * @author Jinhai
 */
(function(){

	var HTML = [];
	HTML.push('<div class="c-flowitem-bar">');
	HTML.push('<div class="c-flowitem-bk"></div>');
	HTML.push('<div class="c-flowitem-anchor"><div class="c-flowitem-anchor-inner"></div></div>');
	HTML.push('</div>');
	HTML.push('<div class="c-flowitem-label"></div>');
	HTML = HTML.join('');

	Cmp.define('Cmp.flow.FlowItem',{
		extend : 'Cmp.Widget',
		cls : true,
		factory : function(ext, reqs){
			var superclass = ext.prototype;
			return Cmp.extend(ext, {
			
				/**
				 * @public
				 * 
				 * @param {String} label 
				 */
				setLabel : function(label){
					var me = this;
					if(me.labelBox){
						me.labelBox.update(label);
					}
					else{
						me.label = label;
					}
				}, 
				/**
				 * @public
				 * 
				 * @param {String} text 1<i class="fa fa-checked"></i> HTML
				 */
				setAnchorText : function(text){
					var me = this;
					if(me.anchorBox){
						me.anchorBox.update(text);
					}
					else{
						me.anchor = text;
					}
				},
				/**
				 * @public
				 * 
				 */
				active : function(){
					this.setState('active');
				},
				/**
				 * @public
				 * 
				 */
				disable : function(){
					this.setState('disabled');
				},
				/**
				 * @public
				 * 
				 */
				enable : function(){
					this.setState('enabled');
				},
				/**
				 * @public
				 * 
				 *
				 * @param {String} state ()'active'|'disabled'|'enabled'; :'enabled'
				 */
				setState : function(state){
					var me = this;
					if('active' !== state && 'disabled' !== state){
						state = 'enabled';
					}
					if(me.state === state){
						return ;
					}
					if('enabled' !== me.state){
						me.el.removeClass('c-flowitem-'+me.state);
					}
					if('enabled' !== state){
						me.el.addClass('c-flowitem-'+state);
					}
					me.state = state;
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,
						cls = me.cls;
					if(isA(cls)){
						cls.unshift('c-flowitem');
					}
					else if(isS(cls)){
						cls = ['c-flowitem', cls];
					}
					else{
						cls = 'c-flowitem';
					}
					me.cls = cls;
					superclass.initComponent.call(me);
				},
				/**
				 * @private
				 * @overwrite
				 */
				doRender : function(){
					var me = this,dom,v;
					superclass.doRender.call(me);
					me.el.update(HTML);
					
					if(true === me.allowedClick){
						me.el.setAttribute('clickable','clickable');
					}
					
					if('disabled' === me.state){
						me.el.addClass('c-flowitem-disabled');
					}
					else if('active' === me.state){
						me.el.addClass('c-flowitem-active');
					}
					else{
						me.state = 'enabled';
					}
					
					dom = me.el.dom.firstChild.childNodes[1].firstChild;
					me.anchorBox = Cmp.get(dom);

					dom = me.el.dom.childNodes[1];
					me.labelBox = Cmp.get(dom);
					
					//
					v = me.anchor;
					delete me.anchor;
					if(isS(v)){
						me.setAnchorText(v);
					}
					
					v = me.label;
					delete me.label;
					if(isS(v)){
						me.setLabel(v);
					}
					
					me.el.on('click', me.onClick, me);
				},
				/**
				 * @private
				 */
				onClick : function(){
					var me = this;
					if(true === me.allowedClick && 'enabled' === me.state && isF(me.handler)){
						me.handler.call(me.scope||me, me);
					}
				}
			});
		}
	});
	
}());