/**
 * 
 * @class Cmp.form.Field
 * @extend Cmp.Widget
 *
 * <p>
 * 
 * @cfg {Object} value ()
 * @cfg {String} emptyText ()
 * @cfg {String} label ()
 * @cfg {String} name ()
 * @cfg {Boolean} disabled ()truefalse
 * @cfg {Boolean} hideLabel ()truefalse
 * @cfg {String/Number} labelWidth (): '5rem'
 * </p>
 *
 * @version 3.0.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){

	Cmp.define('Cmp.form.Field',{
		extend : 'Cmp.Widget',
		cls : true,
		factory : function(ext, reqs){
			var superClazz = ext.prototype;
			
			return Cmp.extend(ext, {
				/**
				 * 
				 */
				enable : function(){
					var me = this;
					if(me.disabled){
						me.doEnable();
						me.disabled = false;
					}
				},
				/**
				 * 
				 * @final #doDisable()
				 */
				disable : function(){
					var me = this;
					if(!me.disabled){
						me.doDisable();
						me.disabled = true;
					}
				},
				/**
				 * true
				 */
				isDisabled : function(){
					return this.disabled;
				},
				/**
				 * @abstract
				 * 
				 * @param {Object} value
				 */
				setValue : Cmp.emptyFn,
				/**
				 * @abstract
				 * 
				 * @return {Object} value
				 */
				getValue : Cmp.emptyFn,
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this;
					var cls = ['c-fd'];
					if(isS(me.cls)){
						cls.push(me.cls);
					}
					else if(isA(me.cls)){
						cls = cls.concat(me.cls);
					}
					me.cls = cls;
					superClazz.initComponent.call(me);
					
					me.addEvents(
						/**
						 * @event
						 * 
						 * @param {Object} value 
						 * @param {Field} this
						 */
						'changed'
					);
				},
				/**
				 * @private
				 * @overwrite
				 */
				doRender : function(){
					var me = this;
					superClazz.doRender.call(me);
					//
					me.hideLabel = !!me.hideLabel;
					var el, lw = '0px';
					if(!me.hideLabel){
						lw = me.labelWidth;
						if(isN(lw)){
							lw = lw+'px'
						}
						if(!isS(lw)){
							lw = '5rem';
						}
						el = me.el.createChild({
							cls : 'c-fd-lb',
							style : {
								width : lw
							},
							html : '<strong>'+(me.label||'')+'</strong><i></i><b></b>'
						});
						me.labelBox = Cmp.get(el.dom.firstChild);
					}
					
					el = me.el.createChild({
						cls : 'c-fd-inbox',
						style : {
							paddingLeft : lw
						},
						html : '<div class="c-fd-inwarp"></div>'
					});
					me.inputBox = Cmp.get(el.dom.firstChild);
					me.renderInput(me.inputBox);
					
					me.errorBox = me.inputBox.createChild({
						cls : 'c-fd-error-msg'
					});
					
					me.el.setStyle({
						minWidth : lw
					});
					
					el = !!me.disabled;
					me.disabled = false;
					if(el){
						me.disable();
					}
					
					if(me.value != undefined){
						el = me.value;
						me.value = undefined;
						me.setValue(el);
					}
				},
				/**
				 * @private
				 * 
				 * 
				 */
				doEnable : function(){
					var me = this;
					if(me.el){
						me.el.removeClass('c-fd-disabled');
					}
				},
				/**
				 * @private
				 * 
				 * 
				 */
				doDisable : function(){
					var me = this;
					if(me.el){
						me.el.addClass('c-fd-disabled');
					}
				},
				/**
				 * 
				 */
				showError : function(msg){
					var me = this;
					if(!me._hasError){
						me._hasError = true;
						me.el.addClass('c-fd-error');
					}
					me.updateErrorMsg(msg);
				},
				/**
				 * 
				 */
				hideError : function(){
					var me = this;
					if(me._hasError){
						me._hasError = false;
						me.el.removeClass('c-fd-error');
					}
					me.updateErrorMsg();
				},
				/**
				 * @private
				 * 
				 */
				updateErrorMsg : function(msg){
					var me = this;
					if(me.errorBox){
						me.errorBox.update(msg||'');
					}
				},
				/**
				 * @abstract
				 * @private
				 * 
				 *
				 * @param {Element} el InputDom;div
				 */
				renderInput : Cmp.emptyFn
			});
		}
	});
}());