/**
 * @static Cmp.util.Dates
 * 
 * 
 * @version 1.0.0
 * @since 2016-6-10
 * @author Jinhai
 */
(function(){


	var Rel = {
		/**
		 * 20160618
		 *
		 * @param {Date} date ()
		 * @return {String} 
		 */
		toDateStringForCh : function(date){
			if(!isD(date)){
				return '';
			}
			var rel = [],v;
			//
			rel.push(date.getFullYear());	
			rel.push('');
			// 
			v = date.getMonth()+1;
			if(v < 10){
				rel.push('0');	
			}
			rel.push(v);
			rel.push('');
			//
			v = date.getDate();
			if(v < 10){
				rel.push('0');	
			}
			rel.push(v);
			rel.push('');
			return rel.join('');
		},
		/**
		 * 201606
		 * @param {Date} date ()
		 * @return {String} 
		 */
		toMonthStringForCh : function(date){
			if(!isD(date)){
				return '';
			}
			var rel = [],v;
			//
			rel.push(date.getFullYear());	
			rel.push('');
			// 
			v = date.getMonth()+1;
			if(v < 10){
				rel.push('0');	
			}
			rel.push(v);
			rel.push('');
			return rel.join('');
		},
		/**
		 * ('YYYY-MM-DD')
		 * @param {Date} date ()
		 * @return {String} 
		 */
		toDateString : function(date){
			if(!isD(date)){
				return '';
			}
			var rel = [],v;
			//
			rel.push(date.getFullYear());	
			// 
			rel.push('-');
			v = date.getMonth()+1;
			if(v < 10){
				rel.push('0');	
			}
			rel.push(v);
			//
			rel.push('-');
			v = date.getDate();
			if(v < 10){
				rel.push('0');	
			}
			rel.push(v);
			return rel.join('');
		},
		/**
		 * 
		 * @param {Number} year ()2016
		 * @param {Number} month ()101211 
		 */
		getDaysOfMonth : function(year, month){
			switch(month){
				//
				case 3:
				//
				case 5:
				//
				case 8:
				//
				case 10:
					return 30;
				//	
				case 1:
					if(0 === year % 400 || (0 !== year % 100 && 0 === year % 4)){
						//
						return 29;
					}
					else{
						//
						return 28;
					}
				//	
				default :
					return 31	
			}
		}
	};
	
	Cmp.define('Cmp.util.Dates',{
		factory : function(){
			return Rel;
		}
	
	});
}());