/**
 * 
 * <p>
 *  
 *
 * @abstract
 * @class Cmp.util.TableModel
 * @extend Cmp.util.Observable
 * 
 * @subclass Cmp.util.ArrayTableModel
 * @subclass Cmp.util.MapTableModel
 * 
 * @version 1.0.0
 * @since 2015-10-31
 * @author Jinhai
 */
(function(){
	var UV = Cmp.util.ValueHelper,
		Record;
	/**
	 * 
	 * @param {Array} records 
	 * @param {sortInfo} sortInfo keydir;('asc''desc','asc');
	 */
	var sort = function(records, sortInfo){
		if(!isA(records) || records.length == 0
			||  !sortInfo || !sortInfo.key){
			return records;
		}
		
		var k = sortInfo.key,
			d = 'desc' == sortInfo.dir;
		records.sort(function(r1, r2){
			var v1 = r1.get(k);
			var v2 = r2.get(k);
			return d ? -UV.compare(v1, v2) : UV.compare(v1, v2);
		});			
		return records;
	}
	
	/**
	 * 
	 * @param {Array} records 
	 * @param {Object} filterInfo ()
	 * 		<li> {Function} compare ()RecordRecordtrue;
	 *		<li> {Object} scope ()comparethiscompare
	 * 		<li> {String/Object} key () compare
	 *		<li> {Object} value () comparekey
	 */
	var filter = function(records, filter){
		if(!isA(records) || records.length == 0){
			return [];
		}
		if(!filter){
			return records.slice(0);
		}
		
		var rel,
			comp = filter.compare;
		if(isF(comp)){
			rel = [];
			Cmp.each(records, function(r){
				if(comp.call(filter.scope || {}, r)){
					rel.push(r);
				}
			});
			return rel;
		}
		
		if(isS(filter.key)){
			var o = {};
			o[filter.key] = filter.value
			filter.key = o;
		}
		if(isO(filter.key)){
			var i,v,av,c,
				o = filter.key,
				rel = [];
			Cmp.each(records, function(r){
				c = true;
				for(i in o){
					av = o[i];
					v = r.get(i);
					if(0 !== UV.compare(v, av)){
						c = false;
					}
				}
				if(c){
					rel.push(r);
				}
			});
			return rel;
		}
		
		return records.slice(0);
	}
	
	
	/**
	 * 
	 * Reocrdvalues
	 * 
	 * @return {Boolean/Array} falseRecord
	 */
	var doInsert = function(values){
//		putLog('TableModel#doInsert>');
		if(isO(values) && !isA(values)){
			values = [values];
		}
		if(!isA(values)){
			return false;
		}	
		var me = this,
			i = 0,
			len = values.length,
			rel = [],
			r,rid;
		
		for(;i<len;i++){
			r = me.createRecord(values[i]);
			if(!r){
				continue;
			}
			r.on('changed', onRecordChanged, me);
			
			if(true === me.editing){
				//
				r.beginEdit();
			}
			me.values.push(r);
			rel.push(r);
			if(false !== me.idKey){
				rid = r.get(me.idKey);
				me.idValues[rid] = r;
			}
		}
		
		if(me.filterInfo){
			rel = filter(rel, me.filterInfo);
		}
		
		
		if(me.resultValues && rel.length > 0){
			Cmp.each(rel, function(r){
				me.resultValues.push(r);
			});
			//
			if(me.sortInfo){
				me.resultValues = sort(me.resultValues, me.sortInfo);
			}
		}
		
		return 0 === rel.length ? false : rel;
	}
	/**
	 * 
	 * @param {Mixed} records RecordRecord Id 
	 * @return {Boolean/Array} falseRecord
	 */
	var doRemove = function(records){
//		putLog('TableModel#doRemove>');
		if(isS(records) 
			|| (records instanceof Record)){
			records = [records];
		}
		if(!isA(records)){
			return false;
		}
		
		var me = this,
			vs = me.values,
			idk = me.idKey,
			idvs = me.idValues,
			i = 0,
			len = records.length,
			rel = [],
			v,r,ri;
			
		for(;i<len;i++){
			v = records[i];
			if(isS(v)){
				r = idvs ? idvs[v] : false;
			}
			else if(v instanceof Record){
				r = v;
				v = idk ? r.get(idk) : false;
			}
			
			//
			ri = vs.indexOf(r);
			if(ri > -1){
				vs.splice(ri, 1);
				//
				if(idvs){
					delete idvs[v];
				}
				
				//
				r.purgeListeners();
				
				//
				rel.push(r);
			}
		}
		
		if(rel.length > 0){
			//
			me.resultValues = filter(me.values, me.filterInfo);
			//
			me.resultValues = sort(me.resultValues, me.sortInfo);
			return rel;	
		}
		else{
			return false;
		}
		
	}
	
	/**
	 * 
	 */
	var onRecordChanged = function(r){
		var me = this;
		if(true !== me.editing){
			//
			
			//
			me.resultValues = filter(me.values, me.filterInfo);
			//
			me.resultValues = sort(me.resultValues, me.sortInfo);
			
			me.fireEvent('edit', me, r);
			me.fireEvent('changed', 'edit', me, r);
		}
	}
	/**
	 * 
	 */
	var EDIT_HANDLER = {
		insert : doInsert,
		remove : doRemove
	}; 
	
	
	/**
	 * true
	 * 
	 */
	var compareFilter = function(of, nf){
		if(of == nf){
			return true;
		}
		if(of == undefined || nf == undefined){
			return false;
		}
		//
		var getNames = function(o){
			var ms = [];
			for(var i in o){
				ms.push(i);
			}
			return ms;
		}
		
		var oms = getNames(of),
			nms = getNames(nf);
		//false
		if(!UV.compareArray(oms, nms)){
			return false;
		}	
		var n,ov,nv;
		for(var i=0, len = oms.length; i< len;i++){
			n = oms[i];
			ov = of[n];
			nv = nf[n];
			if(0 !== UV.compare(ov, nv)){
				return false;
			}
		}
		return true;
	}	
	Cmp.define('Cmp.util.TableModel',{
		extend : 'Cmp.util.Observable',
		requires : [
			'Cmp.util.Record'
		],
		factory : function(ext, reqs){
			var superClass = ext.prototype;
			Record = reqs[0];
			return Cmp.extend(ext, {
				/**
				 * @cfg {Array} keies ()()
				 */
				/**
				 * @cfg {Number} idIndex ()IDkeiesgetByIdundefined;
				 */
				/**
				 * @cfg {Array} values ()Record
				 */
				/**
				 * @cfg {Object} sort ()keydir;('asc''desc','asc');
				 */ 
				/**
				 * @cfg {Object} filter ()
				 * 		<li> {Function} compare ()RecordRecordtrue;
				 *		<li> {Object} scope ()comparethiscompare
				 * 		<li> {String/Object} key () compare
				 *		<li> {Object} value () comparekey
				 */
				/**
				 * @constructor
				 */
				constructor : function(config){
					var me = this;
					config = config || {};
					me.keies = isA(config.keies) ? config.keies : [];
					me.values = [];
					
					//ID
					me.idKey = false;
					//ID{Record}falseID
					me.idValues = false; 
					
					if(isN(config.idIndex) && config.idIndex < me.keies.length){
						me.idValues = {};
						me.idKey = me.keies[config.idIndex];
					}
					
					doInsert.call(me, config.values);
					
					me.resultValues = filter(me.values, config.filter);
					me.filterInfo = config.filter;
					
					me.resultValues = sort(me.resultValues, config.sort);
					me.sortInfo = config.sort;
					
					
					me.addEvents(
						/**
						 * @event
						 * 
						 * @param {TableModel} this
						 */
						'sort',
						/**
						 * @event
						 * 
						 * @param {TableModel} this
						 */
						'filter',
						/**
						 * @event
						 * 
						 * @param {TableModel} this
						 * @param {Record} record 
						 */ 
						'edit',
						/**
						 * @event
						 * 
						 * @param {TableModel} this
						 * @param {Array} records 
						 */
						'insert',
						/**
						 * @event
						 * 
						 * 
						 * @param {TableModel} this
						 * @param {Array} records 
						 */ 
						'remove',
						/**
						 * @event
						 * 
						 * @param {TableModel} this
						 */
						'reload',
						/**
						 * @event
						 * 
						 * 1> 
						 * 2> 
						 * 3> 
						 * 4> 
						 * 
						 * @param {String} type sort,filter,edit,reload,insert,remove,commit
						 * @param {TableModel} this 
						 * @param {Mixed} records insert,remove,edit
						 */
						'changed'
					);
					superClass.constructor.call(me, config);
				},
				/**
				 * 
				 * <p>
				 * 
				 * 'insert''changed'
				 * endEditcommit
				 *
				 * @param {Array} values Record
				 * @return {TableModel} this
				 */
				insert : function(values){
					var me = this;
		//			putLog('TableModel#insert> values:'+JSON.stringify(values));
					if(true === me.editing){
						me.editBatch.push({
							invoke : 'insert',
							params : values
						});
					}
					else{
						var rs = doInsert.call(me, values);
						if(rs){
							me.fireEvent('insert',me, rs);
							me.fireEvent('changed', 'insert', me, rs);
						}
					}
					return me;
				},
				/**
				 * 
				 * <p>
				 * 'remove''changed'
				 * 
				 * 
				 * @param {Mixed} records RecordRecord Id 
				 * @return {TableModel} this
				 */
				remove : function(records){
					var me = this;
					if(true === me.editing){
						me.editBatch.push({
							remove : 'remove',
							values : records
						});
					}
					else{
						var rs = doRemove.call(me, records);
						if(rs){
							me.fireEvent('remove',me, rs);
							me.fireEvent('changed', 'remove', me, rs);
						}
					}
					return me;
				},
				/**
				 * 
				 * <p>
				 *  
				 * remove'remove''changed'
				 * 
				 * @param {Array/Number} index 
				 * @return {TableModel} this
				 */
				removeByIndex : function(index){
					var me = this;
					if(true !== me.editing){
						index = isN(index) ? [index] : index;
						var rs = [],
							i = 0,
							len = isA(index) ? index.length : 0,
							crs = me.resultValues,
							count = crs.length,
							ci;
							
						for(;i<len;i++){
							ci = index[i];
							if(ci > -1 && ci < count){
								rs.push(crs[ci]);
							}
						}
						
						rs = doRemove.call(me, rs);
						if(rs){
							me.fireEvent('remove',me, rs);
							me.fireEvent('changed', 'remove', me, rs);
						}	
					}
					return me;
				},
				/**
				 * 
				 * <p>
				 * <p>
				 * 'reload''changed'
				 * <p>
				 * 
				 * 
				 * 
				 * @param {Array} values Record
				 * @return {TableModel} this
				 */
				reload : function(values){
					var me = this;
					
					if(true === me.editing){
						//
						me.editBatch.splice(0,me.editBatch.length);
					}
					
					//
					var vs = me.values,
						len = vs.length;
					while(len--){
						vs[len].purgeListeners();
					}	
					if(me.idKey){
						me.idValues = {};
					}
					me.values = [];	
					me.resultValues = [];
					
					if(!Cmp.isArray(values) || values.length == 0){
						me.fireEvent('reload', me);
						me.fireEvent('changed', 'reload', me);
						return me;
					}
					
					var rs = doInsert.call(me, values);
					if(rs){
						me.fireEvent('reload', me);
						me.fireEvent('changed', 'reload', me);
					}
					
					return me;
				},
				/**
				 * 
				 *
				 * @param {Number} index 00
				 * @param {Number} limit 
				 * @return {Array} Record0
				 */
				getRecords : function(index, limit){
					var me = this,
						crs = me.resultValues,
						end;
					
					index = isN(index) ? index : 0;
					if(index < 0 || index >= crs.length){
						//0
						return [];
					}
					
					limit = isN(limit) ? limit : crs.length;
					end = index+limit;
					if(end > crs.length){
						end = crs.length;
					}
					
						
					return crs.slice(index, end);
				},
				/**
				 * 
				 * @param {String} key ()
				 * @param {String} dir ()'asc'||'desc'; 'asc'
				 * @return {TableModel} this
				 */
				sort : function(key, dir){
					var me = this,
						info = me.sortInfo;
		
		//			putLog('TableModel#sort>key:'+key+', dir:'+dir);
					
					if(info && info.key === key && info.dir === dir){
						return me;
					}
					me.sortInfo = {
						key : key,
						dir : dir
					};
					sort.call(me, me.resultValues, me.sortInfo);
					me.fireEvent('sort', me);
					me.fireEvent('changed', 'sort', me);
					return me;
				},
				/**
				 * 
				 * <p>
				 * 
				 *
				 * @param {String/Object/Function} key ()
				 *							  value
				 *							  
				 *							  Recordtrue
				 * @param {Object} value 
				 * @return {Array} Record
				 */
				find : function(key, value){
					var me = this,
						crs = me.resultValues,
						callFn,
						rel = [];
					
					if(isS(key)){
						callFn = function(r){
							return value === r.get(key);
						}
					}
					else if(isO(key)){
						callFn = function(r){
							var rel = true;
							for(var i in key){
								value = r.get(i);
								if(value !== key[i]){
									rel = false;
									break;
								}
							}
							return rel;
						}
					}
					else if(isF(key)){
						callFn = key;
					}	
					
					if(isF(callFn)){
						Cmp.each(crs, function(r){
							if(true === callFn(r)){
								rel.push(r);
							}
						});
					}
					return rel;
				},
				/**
				 * 
				 * getRecords(), find(), getCount(), getAt()
				 *
				 *
				 * @param {Object} filterInfo ()
				 * 		<li> {Function} compare ()RecordRecordtrue;
				 *		<li> {Object} scope ()comparethiscompare
				 * 		<li> {String/Object} key () compare
				 *		<li> {Object} value () comparekey
				 * @return {TableModel} this
				 */
				filter : function(filterInfo){
					var me = this;
					
		//			putLog('TableModel#filter>filterInfo:'+JSON.stringify(filterInfo));
					if(compareFilter(me.filterInfo, filterInfo)){
						return me;
					}
					
					me.filterInfo = filterInfo;
					//
					me.resultValues = filter(me.values, me.filterInfo);
					//
					me.resultValues = sort(me.resultValues, me.sortInfo);
					
					//filter 
					me.fireEvent('filter', me);
					me.fireEvent('changed', 'filter', me);
					
					return me;
				},
				/**
				 * getRecords(index, 1)
				 * 
				 * @param {Number} index 0
				 * @return {Record} Recordunfined
				 */
				getAt : function(index){
					var me = this,
						crs = me.resultValues;
					index = isN(index) ? index : 0;
					if(index < 0){
						//0
						return undefined;
					}
					
					return crs[index];
				},
				/**
				 * RecordIDID
				 * <p>
				 * 
				 *
				 * @return {Record} Recordunfined
				 */
				getById : function(recordId){
					var me = this;
					return me.idValues ? me.idValues[recordId] : undefined;
				},
				/**
				 * 
				 */
				count : function(){
					return this.resultValues.length;
				},
				/**
				 * 
				 * commitendEdit
				 * 'changed'
				 * @return {TableModel} this
				 */
				beginEdit : function(){
					var me = this;
					if(!me.editing){
						me.editBatch = [];
						
						var len = me.values.length;
						while(len--){
							me.values[len].beginEdit();
						}
					}
					me.editing = true;
					return me;
				},
				/**
				 * 
				 * @return {TableModel} this
				 */
				cancelEdit : function(){
					var me = this;
					delete me.editBatch;
					var len = me.values.length;
					while(len--){
						me.values[len].cancelEdit();
					}
					me.editing = false;
					return me;
				},
				/**
				 * 
				 * <p>
				 * 'changed'
				 * @return {Boolean} true
				 */
				commitEdit : function(){
					var me = this;
					if(true === me.editing){
						var bs = me.editBatch.splice(0,me.editBatch.length),
							i = 0,
							len = bs.length,
							b,call,c = false;
						
						//
						var len = me.values.length;
						while(len--){
							if(true == me.values[len].commit()){
								c = true;
							}
						}	
						if(c){
							//
							me.resultValues = filter(me.values, me.filterInfo);
							//
							me.resultValues = sort(me.resultValues, me.sortInfo);
						}
							
						for(;i<len;i++){
							b = bs[i];
							call = EDIT_HANDLER[b.invoke];
							if(isF(call) && false !== call.apply(me, b.params)){
								c = true;
							}
						}
						
						if(true === c){
							me.fireEvent('changed', 'commit', me);
						}
						return c;	
					}
					return false;
				},
				/**
				 * 
				 * <p>
				 * 'changed'
				 * @return {TableModel} this
				 */
				endEdit : function(){
					var me = this;
					me.commitEdit();
					me.cancelEdit();
				},
				/**
				 * true
				 */
				isEditing : function(){
					return true === this.editing;
				},
				/**
				 * @private
				 * Record
				 * @param {Object} value insertvalues
				 * @return {Record} Recordundefinednullfalse
				 */
				createRecord : Cmp.emptyFn
			});
		}
	});
}());