/**
 * @class Cmp.Tooltip
 * @extend Cmp.Widget
 * 
 * 
 * titlex
 * <p>
 * 
 * @cfg {Boolean} closable ()truetrue;
 * @cfg {Number} autoCloseDelay ()1000
 *			closablefalse3000true0 
 * </p>
 * 
 * @version 3.0.0
 * @since 2016-06-10
 * @author Jinhai
 */
(function(){
		var DH = Cmp.util.DomHelper,
		UV = Cmp.util.ValueHelper,
		HTML_TMP,
		html = [];
		
	
	html.push('<div class="c-tip-box">');
	html.push('<div class="c-tip-inner">');
	html.push('<div class="c-tip-icon">');
	html.push('</div><div class="c-tip-body">&nbsp;</div>');
	html.push('<div class="c-tip-close">');
	html.push('</div></div>');
	html.push('<div class="c-tip-arrow"></div></div>');
	html.push('</div>');//End box
	
	HTML_TMP = 	html.join('');
	/**
	 * @private
	 * 
	 * @param {Array} txy 
	 * @param {Object} tsz 
	 * @param {Object} sz 
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfoByTL = function(txy, tsz, sz){
		var x = txy[0],
			y = txy[1]-sz.height;
		
		return {
			xy : [x,y],
			arrowCls : 'c-tip-blarrow'
		}	
	}
	/**
	 * @private
	 * 
	 * @param {Array} txy 
	 * @param {Object} tsz 
	 * @param {Object} sz 
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfoByTR = function(txy, tsz, sz){
		var x = txy[0]-sz.width+tsz.width,
			y = txy[1]-sz.height;
		
		return {
			xy : [x,y],
			arrowCls : 'c-tip-brarrow'
		}	
	}
	/**
	 * @private
	 * 
	 * @param {Array} txy 
	 * @param {Object} tsz 
	 * @param {Object} sz 
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfoByBL = function(txy, tsz, sz){
		var x = txy[0],
			y = txy[1]+tsz.height;
		
		return {
			xy : [x,y],
			arrowCls : 'c-tip-tlarrow'
		}	
	}
	/**
	 * @private
	 * 
	 * @param {Array} txy 
	 * @param {Object} tsz 
	 * @param {Object} sz 
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfoByBR = function(txy, tsz, sz){
		var x = txy[0]-sz.width+tsz.width,
			y = txy[1]+tsz.height;
		
		return {
			xy : [x,y],
			arrowCls : 'c-tip-trarrow'
		}	
	}
	
	/**
	 * @private
	 * 
	 * @param {Array} txy 
	 * @param {Object} tsz 
	 * @param {Object} sz 
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfoByLT = function(txy, tsz, sz){
		var x = txy[0]-sz.width,
			y = txy[1];
		
		return {
			xy : [x,y],
			arrowCls : 'c-tip-rtarrow'
		}	
	}
	
	/**
	 * @private
	 * 
	 * @param {Array} txy 
	 * @param {Object} tsz 
	 * @param {Object} sz 
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfoByLB = function(txy, tsz, sz){
		var x = txy[0]-sz.width,
			y = txy[1]+tsz.height-sz.height;
		
		return {
			xy : [x,y],
			arrowCls : 'c-tip-rbarrow'
		}	
	}
	
	/**
	 * @private
	 * 
	 * @param {Array} txy 
	 * @param {Object} tsz 
	 * @param {Object} sz 
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfoByRT = function(txy, tsz, sz){
		var x = txy[0]+tsz.width,
			y = txy[1];
		
		return {
			xy : [x,y],
			arrowCls : 'c-tip-ltarrow'
		}	
	}
	
	/**
	 * @private
	 * 
	 * @param {Array} txy 
	 * @param {Object} tsz 
	 * @param {Object} sz 
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfoByRB = function(txy, tsz, sz){
		var x = txy[0]+tsz.width,
			y = txy[1]+tsz.height-sz.height;
		
		return {
			xy : [x,y],
			arrowCls : 'c-tip-lbarrow'
		}	
	}
	
	var POS_HANDLERS = {
		'tl':getTooltipPosInfoByTL,
		'tr':getTooltipPosInfoByTR,
		'bl':getTooltipPosInfoByBL,
		'br':getTooltipPosInfoByBR,
		'lt':getTooltipPosInfoByLT,
		'lb':getTooltipPosInfoByLB,
		'rt':getTooltipPosInfoByRT,
		'rb':getTooltipPosInfoByRB
	};

	/**
	 * @private
	 * 
	 * 
	 */
	var getTooltipPosInfoByAuto = function(txy, tsz, sz){
		var me = this,r;
		//TODO 
		//
//		r = getTooltipPosInfoByBL.call(me, txy, tsz, sz);
		//
//		r = getTooltipPosInfoByBR.call(me, txy, tsz, sz);
		//
//		r = getTooltipPosInfoByTL.call(me, txy, tsz, sz);
		//
//		r = getTooltipPosInfoByTR.call(me, txy, tsz, sz);
		
		r = getTooltipPosInfoByRT.call(me, txy, tsz, sz);
		return r;
	}

	/**
	 * @private
	 *
	 * 
	 *
	 * @reutrn {Object} 
	 * 	<li> {String} arrowCls : CSS
	 *	<li> {Array} xy 
	 */
	var getTooltipPosInfo = function(target, pos, showArrow){
		var me = this,
			sz = me.el.getSize(),
			txy,tsz,dom,fn,rel;
		if(!target){
			return false;
		}
		if(isE(target)){
			dom = target;
		}
		else if(isO(target)){
			//Widget
			if(isF(target.render)){
				dom = target.el.dom;
			}
			else{
				dom = target.dom;
			}
		}
		else if(isS(target)){
			dom = document.getElementById(target);
		}
		//target
		if(!dom){
			return false;
		}	
		txy = DH.getXY(dom);
		tsz = {
			width : DH.getWidth(dom),
			height : DH.getHeight(dom)
		};
		fn = POS_HANDLERS[pos];
		if(!isF(fn)){
			fn = getTooltipPosInfoByAuto;
		}
		rel = fn.call(me, txy,tsz,sz);
		if(false === showArrow){
			rel.arrowCls = 'c-tip-noarrow';
		}
		return rel;
	}
	
	
	Cmp.define('Cmp.Tooltip',{
		extend : 'Cmp.Widget',
		cls : true,
		factory : function(ext, reqs){
			var superclass = ext.prototype;
			return Cmp.extend(ext, {
				/**
				 * @public
				 * 
				 * 'show'
				 * 
				 * @param {HTMLElement/Element/Widget/String} el ()HTMLElementElementWidgetHTMLElementID
				 * @param {String} pos ()
				 * 		<li> auto : ()
				 *		<li> bl : 
				 *		<li> br : 
				 *		<li> tl : 
				 *		<li> tr : 
				 *		<li> lt : 
				 *		<li> lb : 
				 *		<li> rt : 
				 *		<li> rb : 
				 * @param {Boolean} showArrow ()truetrue
				 */
				showBy : function(el, pos, showArrow){
					var me = this;
					if(!me.rendered){
						me.render(Cmp.getBody());
						window.setTimeout(function(){
							me.showBy(el, pos, showArrow);
						},10);
						return ;
					}
					else{
						me.show();
					}
					var ps = getTooltipPosInfo.call(me, el, pos, showArrow),
						v;
					if(!ps){
						return ;
					}
					
					//
					v = ps.arrowCls;
					if(me._arrowCls !== v){
						if(me._arrowCls){
							me.el.removeClass(me._arrowCls);
						}
						me.el.addClass(v);
						me._arrowCls = v;
					}
					
					//
					v = ps.xy;
					me.el.setStyle({
						left : v[0]+'px',
						top : v[1]+'px'
					});
					//
					me.fireEvent('show', me);
				},
				/**
				 * @public
				 * @overwrite
				 */
				show : function(){
					var me = this;
					superclass.show.call(me);
					if(me.autoHideTask){
						me.autoHideTask.run();
					}
				},
				/**
				 * @public
				 * @overwrite
				 */
				hide : function(){
					var me = this;
					if(me.autoHideTask){
						me.autoHideTask.cancel();
					}
					superclass.hide.call(me);
					//
					me.fireEvent('hide', me);
				},
				/**
				 * 
				 *
				 * @param {Object/String} tip 
				 *		{String} text 
				 *		{Array/String} icon CSS
				 */
				setTooltip : function(tip){
					var me = this,
						tx,ic;
					
					if(!me.rendered){
						me.initTip = tip;
						return ;
					}
					
					if(isS(tip)){
						tx = tip;
					}	
					else if(isO(tip)){
						tx = tip.text;
						ic = tip.icon;
					}
					
					me.bodyBox.update(tx||'&nbsp;');
					if(isS(ic) || isA(ic)){
						me.el.removeClass('c-tip-noicon');
						
						var cs = ['c-tip-icon'];
						if(isS(ic)){
							cs.push(ic);
						}
						else{
							cs = cs.concat(ic);
						}
						
						me.iconBox.setClass(cs);
					}
					else{
						me.el.addClass('c-tip-noicon');
					}
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,v;
					superclass.initComponent.call(me);
					var cls = ['c-tip'];
					if(isS(me.cls)){
						cls.push(me.cls);
					}
					else if(isA(me.cls)){
						cls = cls.concat(me.cls);
					}
					me.cls = cls;
					
					me.addEvents(
						/**
						 * @event
						 * 
						 * @param {Tooltip} this
						 */
						'show',
						/**
						 * @event
						 * 
						 * @param {Tooltip} this
						 */
						'hide'
					);
					
					//
					me.closable = me.closable === false ? false : true;
					//
					v = me.closable ? 0:3000;//
					v = UV.toInteger(me.autoCloseDelay, v);
					if(v > 999){
						//
						me.autoHideTask = new Cmp.util.DelayedTask({
							delay : v,
							handler : me.hide,
							scope : me
						})
						delete me.autoCloseDelay;
					}
				},
				/**
				 * @private
				 * @overwrite
				 */
				doRender : function(){
					var me = this,cns;
					superclass.doRender.call(me);
					me.el.setHideModal('display');
					me.el.update(HTML_TMP);
					cns = me.el.dom.firstChild.firstChild.childNodes;
					
					me.box = Cmp.get(me.el.dom.firstChild);
					me.iconBox = Cmp.get(cns[0]);
					me.bodyBox = Cmp.get(cns[1]);
					me.closeBox = Cmp.get(cns[2]);
					me.closeBox.on('click', me.onClickClose, me);
					
					if(!me.closable){
						me.el.addClass('c-tip-noclose');
					}
					
					if(me.initTip){
						var tx,ic;
						if(isS(me.initTip)){
							tx = me.initTip;
						}
						else if(isO(me.initTip)){
							tx = me.initTip.text;
							ic = me.initTip.icon;
						}
						
						me.bodyBox.update(tx || '&nbsp;');
						if(ic){
							me.iconBox.addClass(ic);
						}
						else{
							me.el.addClass('c-tip-noicon');
						}
						delete me.initTip;
					}
					else{
						me.el.addClass('c-tip-noicon');
					}
				},
				/**
				 * @private
				 */
				onClickClose : function(){
					this.hide();
				}
			});
		}
	});
}());