/**
 * 
 * 
 *
 * @static Cmp.WindowMng
 * 
 * @version 2.0.0
 * @since 2016-06-10
 * @author Jinhai
 */
(function(){

	var ACTIVE_Z_INDEX = 100000,				//Windowz-index
		MASK_Z_INDEX = 99990,					//mask
		UNACTIVE_Z_INDEX = 99980,				//Windowz-index
		ACTION_MASK_Z_INDEX = ACTIVE_Z_INDEX+5;	//divz-index
	
	var getDefaultXY = function(box, win){
		var bw = box.getWidth(),
			bh = box.getHeight(),
			ww = win.box.getWidth(),
			wh = win.box.getHeight(),
			x = (bw - ww) / 2,
			y = (bh - wh) / 2;
		x = x > 0 ? x : 0,
		y = y > 0 ? y : 0;
			
		return [x,y];	
	}	
	
	/**
	 * 
	 */
	var setWinLeftAndTop = function(win, xy, isCenter){
		var x,y;
		if(true === isCenter){
			var w = win.el.getWidth(),
				h = win.el.getHeight();
		}
		else{
			x = xy[0];
			y = xy[1];
			if(isN(x)){
				x = x+'px';
			}
			if(isN(y)){
				y = y+'px';
			}
			
			win.el.setStyle({
				top : y,
				left : x
			});
		}
	}
	
	var winArray = [],
		activeWin;

	/**
	 * 
	 * @param {Window} win 
	 */
	var sortWindowLayer = function(win){
		var me = this,wid,i,len;
		if(win){
			wid = win.getId();
			if(activeWin){
				winArray.push(activeWin);
			}
			
			for(i=0,len = winArray.length;i<len;i++){
				if(winArray[i] == wid){
					winArray.splice(i, 1);
				}
			}
			
		}
		else if(winArray.length > 0){
			wid = winArray.pop();
		}
		
		if(wid){
			var sm = false;
			win = Cmp.getCmp(wid);
			if(win.modal){
				sm = true;
			}
			win.el.removeClass('xp-win-unactive');
			win.el.setStyle('zIndex', ACTIVE_Z_INDEX);
			activeWin = wid;
			
			for(i=0,len = winArray.length;i<len;i++){
				wid = winArray[i];
				win = Cmp.getCmp(wid);
				if(win.modal){
					sm = true;
				}
				win.el.addClass('xp-win-unactive');
				win.el.setStyle('zIndex', UNACTIVE_Z_INDEX-len+i);
			}
			if(!sm){
				me.hideMask();
			}
		}
		else{
			activeWin = false;
			me.hideMask();
		}
	}

	
	var Rel = {
		/**
		 * 
		 * 
		 */
		getActiveMask : function(){
			//TODO
			var mask = Rel.activeMask;
			if(!mask){
				mask = Rel.getWindowsBox().createChild({
					cls : 'c-win-actmask',
					style : {
						'zIndex' : ACTION_MASK_Z_INDEX
					}
				});
				mask.setHideModal('display');
				Rel.activeMask = mask;
			}
			return mask;
		},
		/**
		 * 
		 */	
		activeWindow : function(win){
			//TODO
		},
		/**
		 * 
		 * @param {Window} win ()
		 * @param {Array} xy ()[left,top]
		 *		style
		 * @param {Boolean} isCenter truefalse
		 */
		showWindow : function(win, xy, isCenter){
			var box = Rel.getWindowsBox();
			if(win && isF(win.render)){
				win.render(box);
			}
			//			
			win.el.setStyle('display', 'block');
			
			if(true === win.modal){
				Rel.showMask();
			}
			
			//
			if(!xy){
				xy = getDefaultXY(box, win);
				isCenter = false;
			}
			//
			setWinLeftAndTop(win, xy, isCenter);
			
			//
			sortWindowLayer.call(Rel, win);
			
		},
		/**
		 * 
		 */
		hideWindow : function(win){
			if(!win || !win.el || !isF(win.el.hide)){
				return false;
			}
			var wid = win.getId();
			win.el.hide();
			if(activeWin === wid){
				activeWin = false;
				sortWindowLayer.call(Rel);
			}
			else{
				for(var i=0,len = winArray.length;i<len;i++){
					if(winArray[i] == wid){
						winArray.splice(i, 1);
					}
				}
			}
			return true;
		},
		/**
		 * 
		 * @return {Element} 
		 */
		getWindowsBox : function(){
			var me = this;
			if(!me.windowBox){
				me.windowBox = Cmp.getBody();
			}
			return me.windowBox;
		},
		/**
		 * 
		 */
		showMask : function(){
			if(!Rel.mask){
				Rel.mask = Rel.getWindowsBox().createChild({
					cls : 'c-win-mask',
					style : {
						'zIndex' : MASK_Z_INDEX
					}
				});
				Rel.mask.setHideModal('display');
			}
			else{
				Rel.mask.show();
			}
		},
		/**
		 * 
		 */
		hideMask : function(){
			if(Rel.mask){
				Rel.mask.hide();
			}
		}
	}
	
	Cmp.define('Cmp.WindowMng',{
		factory : function(ext, reqs){
			return Rel;
		}
	});
}());