/**
 * 
 * @class Cmp.form.Trigger
 * @extend Cmp.form.TextField
 * 
 * <p>
 * 
 * @cfg {Object} value ()
 * @cfg {String} emptyText ()
 * @cfg {String} label ()
 * @cfg {String} name ()
 * @cfg {Boolean} disabled ()truefalse
 * @cfg {Boolean} hideLabel ()truefalse
 * @cfg {String/Number} labelWidth (): '5rem'
 * @cfg {Boolean} readOnly true
 * @cfg {String} btnIcon undefined
 * 				CSS'my-btn-icon'CSS '.c-fd-disabled .my-btn-icon'
 * @cfg {String} btnType btnIcon:
 * 				'search'|'left'|'right'|'up'|'down'|'calendar'|'gear'; 'search'
 * </p>
 * 
 * @version 3.0.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){
	var ICON_MAP = {
		'search' : 'fa-search',
		'left' : 'fa-angle-left',
		'right' : 'fa-angle-right',
		'up' : 'fa-angle-up',
		'down' : 'fa-angle-down',
		'calendar' : 'fa-calendar',
		'gear' : 'gear'
	};

	Cmp.define('Cmp.form.Trigger',{
		extend : 'Cmp.form.TextField',
		cls : true,
		
		factory : function(ext, reqs){
			var superClazz = ext.prototype;
			
			return Cmp.extend(ext, {
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this;
					var cls = ['c-trg'];
					if(isS(me.cls)){
						cls.push(me.cls);
					}
					else if(isA(me.cls)){
						cls = cls.concat(me.cls);
					}
					me.cls = cls;
					superClazz.initComponent.call(me);
				},
				/**
				 * @private
				 * @overwrite
				 */
				renderInput : function(box){
					var me = this,cls = ['c-trg-btn','fa'];
					superClazz.renderInput.call(me, box);
					if(isA(me.btnIcon)){
						cls = cls.concat(me.btnIcon);
					}
					else if(isS(me.btnIcon)){
						cls.push(me.btnIcon);
					}
					else{
						cls.push(ICON_MAP[me.btnType] || ICON_MAP['search']);
					}
					me.trigetBtn = box.createChild({
						cls : cls
					});
					me.trigetBtn.on('click', me.onClickTrigger, me);
					if(me.readOnly){
						me.input.on('click', me.onClickTrigger, me);
					}
				},
				/**
				 * @private
				 * @final 
				 */
				onClickTrigger : function(){
					var me = this;
					if(!me.disaebed){
						me.doClickTrigger();
					}
				},
				/**
				 * @private
				 * @abstract 
				 * readOnly
				 */
				doClickTrigger : Cmp.emptyFn
			});
		}
	});
}());