/**
 * 
 *
 * @class Cmp.tree.TreePanel
 * @extend Cmp.Widget
 * 
 * @version 2.1.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){
	/**
	 * 
	 */
	var extendNodes = function(tree, nodes){
		var view = tree.getView(),
			i=0,len = nodes.length,v;
		
		for(;i<len;i++){
			v = view.getNodeView(nodes[i]);
			if(!v || v.isExtended()){
				//
				continue;
			}
			else{
				//
				view.doExtendNode(v, function(){
					extendNodes(tree, nodes);
				});
				
				return;
			}
		}	
	}
	
	Cmp.define('Cmp.tree.TreePanel',{
		extend : 'Cmp.Widget',
		requires : [
			'Cmp.tree.TreeView'
		],
		cls : true,
		factory : function(ext, reqs){
			var superClazz = ext.prototype,
				treeViewClazz = reqs[0];
			
			return Cmp.extend(ext, {
				/**
				 * @cfg {Boolean} useArrow true/false/
				 *		false
				 */
				
				/**
				 * @cfg {TreeNode/Object} root  
				 */
				
				/**
				 * @cfg {Boolean} hidderRoot true
				 */
				
				/**
				 * @cfg {TreeLoader} treeLoader ; 
				 */
				 
				/**
				 * @cfg {String/Array} extendNode 
				 *		
				 */ 
				/**
				 * 
				 * @return {TreeLoader} 
				 */
				getLoader : function(){
					return this.treeLoader;
				},
				/**
				 * 
				 * @parm {TreeNode/Object} node ()id
				 */
				updateNodeView : function(node){
					//TODO
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,
						cls = me.cls;
					
					if(isA(cls)){
						cls.unshift('c-tree');
					}	
					else if(isS(cls)){
						cls = ['c-tree',cls];
					}
					else{
						cls = 'c-tree';
					}
					me.cls = cls;
					superClazz.initComponent.call(me);
					me.addEvents(
						/**
						 * @event
						 * 
						 * @param {TreeNode} node 
						 * @param {TreePanel} this
						 */
						'dblclicknode',
						/**
						 * @event
						 * 
						 * @param {TreeNode} node 
						 * @param {TreePanel} this
						 */
						'selectedchange',
						/**
						 * @event
						 * 
						 * @param {TreeNode} node 
						 * @param {TreePanel} this
						 */
						'extendednode',
						/**
						 * @event
						 * 
						 * @param {TreeNode} node 
						 * @param {TreePanel} this
						 */
						'collectednode'
					);
				},
				/**
				 * @private
				 * 
				 */
				getView : function(){
					var me = this;
					if(!me.view){
						me.view = new treeViewClazz();
					}
					return me.view; 
				},
				/**
				 * @private
				 * @overwrite
				 */
				doRender : function(){
					var me = this;
					superClazz.doRender.call(me);
					var view = me.getView();
					view.render(me, me.el);
					
					var ens = isS(me.extendNode) ? [me.extendNode] : me.extendNode;
					if(isA(ens) && ens.length > 0){
						extendNodes(me, ens);
					}
					delete me.extendNode;
				},
				/**
				 * 
				 */
				getSelected : function(){
					var me = this,
						v = me.getView().activedView;
					if(v){
						return v.node;
					}	
					return undefined;
				}
			});
		}
	});
}());