/**
 * 
 * 
 * @class Cmp.tree.TreeNode
 * @extend Object
 * 
 * @version 2.1.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){
	/**
	 * @constructor
	 */
	var TreeNode = function(cfg){
		cfg = cfg || {};
		var me = this,
			id = cfg.id || buildId(),
			leaf = !!cfg.leaf,
			async = !!cfg.async,
			alwaysReload = !!cfg.alwaysReload,
			_tree;
		if(!async){
			//
			var cns = [],
				cs = cfg.children,
				i=0, len = isA(cs) ? cs.length : 0,
				c;
			for(;i<len;i++){
				c = new TreeNode(cs[i]);
				cns.push(c);
				c.parent = me;
			}
			me.childNodes = cns;	
		}
		
		me.data = {id:id};
		Cmp.apply(me.data, cfg);
		
		delete me.data.leaf;
		delete me.data.async;
		delete me.data.children;
		delete me.data.alwaysReload;
		/**
		 * ID
		 */
		me.getId = function(){
			return id;
		},
		
		/**
		 * true
		 */
		me.isAsync = function(){
			return async;
		}
		/**
		 * true
		 */
		me.isAlwaysReload = function(){
			return async && alwaysReload;
		}
		
		/**
		 * true
		 */
		me.isLeaf = function(){
			return leaf;
		}
		
		/**
		 * TreePanel
		 * 
		 * @param {TreePanel} tree
		 */
		me.bindTree = function(tree){
			_tree = tree;
		},
		/**
		 * 
		 * @return {TreePanel} 
		 */
		me.getTree = function(){
			return _tree;
		}
	}
	TreeNode.prototype = {
		/**
		 * @cfg {String} id ()IDID
		 */
		/**
		 * @cfg {String/Array} icon ()CSS
		 */
		/**
		 * @cfg {String} text ()
		 */
		
		/**
		 * @cfg {Boolean} leaf ()truefalse
		 */
		
		/**
		 * @cfg {Array} children ()asynctrue
		 */
		
		/**
		 * @cfg {Boolean} async (leaftrue)true
		 *		TreeLoaderfalse
		 */
		
		/**
		 * @cfg {Boolean} alwaysReload (leaftrueasynctrue)true
		 *		TreeLoaderfalse
		 */
		
		/**
		 * 
		 */
		getIcon : function(){
			return this.data.icon;
		},
		/**
		 * 
		 * 
		 */
		setIcon : function(icon){
			//TODO
		},
		/**
		 * 
		 * @return {String} text 
		 */
		getText : function(){
			return this.data.text;
		},
		/**
		 * 
		 */
		setText : function(text){
			var me = this,
				t = me.getTree();
			if(t){
				if(me.editing){
					//
					me.editCache.text = text;
				}
				else{
					me.data.text = text;
				}
				t.updateNodeView(me);
			}
			else{
				me.data.text = text;
			}	
		},
		/**
		 * 
		 * @return {TreeNode} 
		 */
		getParent : function(){
			return this.parent;
		},
		/**
		 * 
		 * 0
		 */
		getTreeLayer : function(){
			var me = this;
			if(isN(me.treeLayer)){
				return me.treeLayer;
			}
			else{
				var p = me.getParent();
				if(p){
					me.treeLayer = p.getTreeLayer()+1;
				}
				else{
					me.treeLayer = 0;
				}
				return me.treeLayer;
			}
		},
		/**
		 * 
		 * @param {Function} cb 
		 * @param {Object} scope this
		 */
		getChildren : function(cb, scope){
			var me = this;
			if(me.isLeaf()){
				Cmp.invoke(cb, scope,[]);
			}
			else if(me.isAsync()){
				//
				if(!isA(me.childNodes) || me.isAlwaysReload()){
					//TreeLoader
					var loader = me.getTree();
					if(loader){
						loader = loader.getLoader();
						if(loader && isF(loader.load)){
							loader.load(me, function(os){
								var i=0,
									len = isA(os) ? os.length : 0,
									c,cns = [];
								
								for(;i<len;i++){
									c = new TreeNode(os[i]);
									c.parent = me;
									cns.push(c);
								}
								
								me.childNodes = cns;
								Cmp.invoke(cb, scope,[cns]);
							});
							return ;
						}
					}
					Cmp.invoke(cb, scope,[]);
				}	
			}
			else{
				Cmp.invoke(cb, scope,[me.childNodes]);
			}
		},
		/**
		 * setsubmitEditendEdit
		 */
		startEdit : function(){
			
		},
		/**
		 * 
		 */
		submitEdit : function(){
			//TODO
		},
		/**
		 * 
		 */
		endEdit : function(){
			//TODO
		}
	};
	
	
	Cmp.define('Cmp.tree.TreeNode',{
		factory : function(){
			return TreeNode;
		}
	});
}());