/**
 * @static Cmp.util.AjaxProxy
 * Ajax/
 * 
 *
 * @version 1.0.0
 * @since 2016-06-10
 * @author Jinhai
 */
(function(){

		//Ajax	
	var limitTaskCount = 20,
		//: 30
		timeoutTimes = 30000,
		//
		retryTimes = 3,
		//
		activeCount = 0,
		//
	 	taskList = [];
	 	
	 var toUrlParamString = function(o){
	 	var n,v,rel = [],flag = false,s;
	 	for(n in o){
	 		if(flag){
	 			rel.push('&');
	 		}
	 		else{
	 			flag = true;
	 		}
	 		v = o[n];
	 		rel.push(n);
	 		rel.push('=');
	 		if(isS(v) || isN(v)){
	 			rel.push(v);
	 		}
	 		else if(isB(v)){
	 			rel.push(v?'true':'false');
	 		}
	 		else if(isA(v)){
	 			rel.push(v.join(','));
	 		}
	 		else if(isO(v)){
	 			rel.push(JSON.stringify(v));
	 		}
	 		else if(isD(v)){
	 			// 
				rel.push(v.getFullYear());	
				//
				rel.push('-');
				s = v.getMonth()+1;
				if(s < 10){
					rel.push('0');	
				}
				rel.push(s);
				//	
				rel.push('-');
				s = v.getDate();
				if(sv < 10){
					rel.push('0');	
				}
				rel.push(s);	
				//
				rel.push(' ');
				s = v.getHours();
				if(s<10){
					rel.push('0');
				}
				rel.push(s);
				//
				rel.push(':');	
				s = v.getMinutes();
				if(s<10){
					rel.push('0');
				}
				rel.push(s);
				//
				rel.push(':');	
				s = v.getSeconds();
				if(s<10){
					rel.push('0');
				}
				rel.push(s);
				
	 		}
	 	}
 		return rel.join('');
	 }	

	/**
	 * HTTPIEXMLHttpRequest
	 * 
	 * @param {String} method ()'GET''POST'
	 * @param {String} url ()
	 * @param {String} paramString ()
	 * @param {Object} headerConfig ()
	 * @param {Function} cb ;error
	 *			result  
	 */
	var HttpRequest = function(method, url, paramString, headerConfig, cb){
		var me = this,
			_request,
			_isLowVer = false;
		
		//XMLHttpRequest
		var createHttpRequest = function(){
			if(window.XMLHttpRequest){
				_request = new XMLHttpRequest();
			}
			else if (window.ActiveXObject){
				//
				_request = new ActiveXObject("Microsoft.XMLHTTP");
				_isLowVer = true;
			}	
		}
		me.requst = function(){
			createHttpRequest();
			_request.open(method || 'GET',url,true);
			if('POST' === method && isS(paramString)){
				_request.setRequestHeader("Content-type","application/x-www-form-urlencoded; charset=UTF-8");
			}
			else{
				_request.setRequestHeader("Content-type","charset=UTF-8");
			}
			_request.send(paramString);
			_request.onreadystatechange = function(){
				if(_request.readyState==4){
					//
					if(_request.status==200){
						cb({
							result : _request.responseText
						});
					}
					else{
						if(_request.status >= 500 && _request.status < 600){
							cb({
								error : ''
							});
						}
						else{
							cb({
								error : ''
							});
						}
					}
				}
			}
		}
	}

	var Task = function(cfg){
		var me = this,
			paramString = isO(cfg.param) ? toUrlParamString(cfg.param) : undefined,
			_requsting = false,
			_runTime = 0,
			_tryTime = 0,
			_timeout = 0,
			_querySeq = 0;
		
		
		
		
		/**
		 * @private
		 * 
		 */
		var onTimeout = function(){
			if(_requsting === true){
				if(_runTime < _tryTime){
					putLog(':'+_runTime, 2);
					startRequst();
				}
				else{
					Cmp.invoke(cfg.callback, cfg.scope, [{
						error : ''
					}]);
				}
			}
		}
		
		me.timeoutTask = new Cmp.util.DelayedTask({
			handler : onTimeout,
			scope : me 
		});
		
		var doRequst = function(seq){
			//method, url, paramString, headerConfig, cb
			var url = [cfg.url];
			//
			if(cfg.url.indexOf('?') > -1){
				url.push('&rbtv=');
			}
			else{
				url.push('?rbtv=');
			}
			url.push(new Date().getTime());
			var req = new HttpRequest(cfg.type, url.join(''), paramString, {}, function(result){
				if(_querySeq === seq){
					me.timeoutTask.cancel();
					_requsting = false;
					Cmp.invoke(cfg.callback, cfg.scope, [result]);
				}
			});
			req.requst();
		}
		
		var startRequst = function(){
			_runTime++;
			doRequst(++_querySeq);
			me.timeoutTask.run({delay:_timeout});
		}
		
		/**
		 * 
		 * @param {Number} timeout 
		 * @param {Number} tryTime 
		 */
		me.execute = function(timeout, tryTime){
			if(_requsting){
				return false;
			}
			_requsting = true;
			_tryTime = tryTime;
			_timeout = timeout;
			startRequst();
		}
		
	}
	
	/**
	 * 
	 * 
	 */
	var executeTask = function(){
		if(activeCount < limitTaskCount && taskList.length > 0){
			var t = taskList.shift();
			t.execute(timeoutTimes, retryTimes);
		}
	}

	var Proxy = {
		/**
		 * 30
		 * @param {Number} timeout 10
		 */
		setTimeout : function(timeout){
			if(isN(timeout) && timeout > 10000){
				timeoutTimes = timeout;
			}
			else{
				timeoutTimes = 30000;
			}
		},
		/**
		 * 3
		 * @param {Number} time 1
		 */
		setRetryTime : function(time){
			if(isN(time) && time > 0){
				retryTimes = time;
			}
			else{
				retryTimes = 3;
			}
		},
		/**
		 * 20
		 * @pamra {Number} count 
		 */
		setMaxTaskCount : function(count){
			if(isN(count) && count > 1){
				limitTaskCount = count;
			}
			else{
				limitTaskCount = 20;
			}
		},
		/**
		 * GET
		 *
		 * @param {String} url 
		 * @param {Object} param 
		 *		JSON.stringify
		 * @param {Function} callback 
		 *		{String} error undifinedfalse;
		 *		{String} result  
		 * @param {Object} scope this
		 */
		get : function(url, param, callback, scope){
			//url
			var rel = [url],
				flag, n,v;
			
			if(isO(param)){
				if(url.indexOf('?') > -1){
					rel.push('&');
				}
				else{
					rel.push('?');
				}
				rel.push(toUrlParamString(param));
			}
		
			var task = new Task({
				type : 'GET',
				url : rel.join(''),
				param : undefined,
				callback : callback,
				scope : scope
			});
			taskList.push(task);
			executeTask();
		},
		/**
		 * POST
		 *
		 * @param {String} url 
		 * @param {Object} param 
		 *		JSON.stringify
		 * @param {Function} callback 
		 *		{String} error undifinedfalse;
		 *		{String} result  
		 * @param {Object} scope this
		 */
		post : function(url, param, callback, scope){
			var task = new Task({
				type : 'POST',
				url : url,
				param : param,
				callback : callback,
				scope : scope
			});
			taskList.push(task);
			executeTask();
		}
		
	}
	
	Cmp.define('Cmp.util.AjaxProxy',{
		factory : function(){
			return Proxy;
		}
	});
}());