/**
 * @class Cmp.flow.Flow
 * @extend Cmp.Widget
 *
 * 
 * <p>
 * 
 * @cfg {Array} stepItems 
 *			{String} key 
 *			{String} label 
 *			{String} anchorText +1
 *			{Boolean} allowedClick true;
 * @cfg {Number} activeIndex 00
 * @cfg {Numbe/Width} itemWidth :'6rem'
 * </p>
 * 
 * @version 2.3.0
 * @since 2016-06-10
 * @author Jinhai
 */
(function(){
	Cmp.define('Cmp.flow.Flow',{
		extend : 'Cmp.Widget',
		cls : true,
		requires : [
			'Cmp.flow.FlowItem'
		],
		factory : function(ext, reqs){
			var superclass = ext.prototype,
				FlowItem = reqs[0];
			return Cmp.extend(ext, {
				/**
				 * 0
				 * @parma {Number} ix ;
				 */
				setActiveIndex : function(ix){
					var me = this;
					if(!isN(ix) || me.activeIndex === ix){
						return ;
					}
					var i=0,len = isA(me.flowItems) ? me.flowItems.length : 0,it;
					for(;i<len;i++){
						it = me.flowItems[i];
						if(i < ix){
							//
							it.enable();
						}
						else if(i === ix){
							//
							it.active();
						}
						else {
							//
							it.disable();
						}
					}
					me.activeIndex = ix;
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,
						cls = me.cls;
					if(isA(cls)){
						cls.unshift('c-flow');
					}
					else if(isS(cls)){
						cls = ['c-flow', cls];
					}
					else{
						cls = 'c-flow';
					}
					me.cls = cls;
					superclass.initComponent.call(me);
					me.addEvents(
						/**
						 * @event 
						 * 
						 * @param {String} itemKey 
						 */
						'clickitem'
					);
				},
				/**
				 * @private
				 * @overwrite
				 */
				doRender : function(){
					var me = this;
					superclass.doRender.call(me);
					var its = me.stepItems,
						i = 0,
						len = isA(its) ? its.length : 0,
						it,
						iits = [];
					for(;i<len;i++){
						it = Cmp.apply({}, its[i]); 
						if(me.itemWidth){
							it.width = me.itemWidth; 
						}
						if(!isS(it.anchorText)){
							it.anchor = (i+1)+'';
						}
						else{
							it.anchor = it.anchorText;
						}
						delete it.anchorText;
						it = new FlowItem(it);
						it.render(me.el);
						iits.push(it);
					}	
					delete me.stepItems;
					me.flowItems = iits;
					
					var ix = isN(me.activeIndex) ? me.activeIndex : 0;
					delete me.activeIndex;
					me.setActiveIndex(ix);
				}
			});	
		}
	
	});
	
}());