/**
 * 
 * 
 * @static Cmp.Dialogs
 * @version 2.3.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){
	var bodyMask,dlg
	
	/**
	 * HTML
	 */
	var biuldWaitHtml = function(msg){
		var html = [];
		html.push('<div class="c-dlgs-icon"></div>');
		html.push('<div class="c-dlgs-msgbox"><div class="c-dlgs-msgbox-inner">');
		html.push(msg);
		html.push('</div></div>');
		return html.join('');
	}

	var Rel = {
		/**
		 * 
		 */
		hide : function(){
			if(dlg){
				dlg.hide();
			}
		},
		/**
		 * 
		 * @param {String} msg ;
		 */
		showWait : function(msg){
			if(dlg){
				dlg.hideBoth();
				dlg.updateBody(biuldWaitHtml(msg), 'c-dlgs-wait');
				dlg.show();
			}
		},
		/**
		 * ''
		 * @param {String} msg ;
		 * @param {String} title 
		 */
		showWarn : function(msg, title){
			if(dlg){
				dlg.showBoth(title || '', [{id : 'ok', text :'',handler : function(){
					dlg.hide();
				}}]);
				dlg.updateBody(biuldWaitHtml(msg), 'c-dlgs-warn');
				dlg.show();
			}
		},
		/**
		 * ''
		 *
		 * @param {String} msg ;
		 * @param {String} title 
		 * @param {Function} callback '
		 * @paarm {Object} scope this
		 */
		showSuccess : function(msg, title, callback, scope){
			if(dlg){
				dlg.showBoth(title || '', [{id : 'ok', text :'',handler : function(){
					dlg.hide();
					Cmp.invoke(callback, scope);
				}}]);
				dlg.updateBody(biuldWaitHtml(msg), 'c-dlgs-success');
				dlg.show();
			}
		},
		/**
		 * ''''
		 *
		 * @param {String} msg ;
		 * @param {String} title 
		 * @param {Function} callback 
		 *		''true''false
		 * @paarm {Object} scope this
		 */
		showSuccessForOkcanel : function(msg, title, callback, scope){
			if(dlg){
				dlg.showBoth(title || '', [
					{id : 'ok', text :'',
						handler : function(){
							dlg.hide();
							Cmp.invoke(callback, scope,[true]);
						}
					},{
						id : 'cancel', text :'',
						handler : function(btn){
							dlg.hide();
							Cmp.invoke(callback, scope,[false]);
						}
					}
				]);
				dlg.updateBody(biuldWaitHtml(msg), 'c-dlgs-success');
				dlg.show();
			}
		},
		/**
		 * ''''
		 *
		 * @param {String} msg ;
		 * @param {String} title 
		 * @param {Function} callback 
		 *		''true''false
		 * @paarm {Object} scope this
		 * @param {Object} btns 'ok''OK','''cancel''Cancel',''
		 */
		showSuccessForYesNo : function(msg, title, callback, scope, btns){
			if(dlg){
				dlg.showBoth(title || '', [
					{id : 'ok', text : (btns ? (btns['ok']||'') : ''),
						handler : function(){
							dlg.hide();
							Cmp.invoke(callback, scope,[true]);
						}
					},{
						id : 'cancel', text : (btns ? (btns['cancel']||'') : ''),
						handler : function(btn){
							dlg.hide();
							Cmp.invoke(callback, scope,[false]);
						}
					}
				]);
				dlg.updateBody(biuldWaitHtml(msg), 'c-dlgs-success');
				dlg.show();
			}
		},
		/**
		 * ''''
		 * 
		 * @param {String} msg ;
		 * @param {String} title 
		 * @param {Function} callback 
		 *		''true''false
		 * @paarm {Object} scope this
		 */
		showConfirm : function(msg, title, callback, scope){
			if(dlg){
				dlg.updateBody(biuldWaitHtml(msg), 'c-dlgs-confirm');
				dlg.showBoth(
					title || '', 
					[{
						id : 'ok', text :'',
						handler : function(btn){
							dlg.hide();
							Cmp.invoke(callback, scope,[true]);
						}
					},{
						id : 'cancel', text :'',
						handler : function(btn){
							dlg.hide();
							Cmp.invoke(callback, scope,[false]);
						}
					}]
				);
				dlg.show();
			}
		}
	};
	
	Cmp.define('Cmp.Dialogs',{
		requires : [
			'Cmp.Dialog'
		],
		cls : true,
		factory : function(ext, reqs){
			dlg = new reqs[0]();
			dlg.render(Cmp.getBody());
			dlg.hide();
			return Rel;
		}
	});
}());