/**
 * 
 * @class Cmp.Button
 * @extend Cmp.Widget
 * <p>
 * 
 * @cfg {String/Array} icon ()CSS
 * @cfg {String} text ()
 * @cfg {String} tooltip ()
 * @cfg {Function} handler ()
 * @cfg {Object} scope ()handlerthis
 * @cfg {Boolean} actived ()truefalse
 * @cfg {Boolean} disabled ()truefalse
 * </p>
 * @version 2.3.0
 * @since 2016-05-31
 * @author Jinhai
 */ 
Cmp.define('Cmp.Button',{
	extend : 'Cmp.Widget',
	cls : true,
	
	
	factory : function(ext, reqs){
		//TODO
		var SP = ext.prototype;
		return Cmp.extend(ext, {
			/**
			 * 
			 */
			setText : function(text){
				var me = this;
				me.text = text;
				if(me.textBox){
					me.textBox.update(text||'');
					if(text){
						me.el.removeClass('c-btn-notext');
					}
					else{
						me.el.addClass('c-btn-notext');
					}
				}
			},
			/**
			 * 
			 * @param {Array/String} icon CSS
			 */
			setIcon : function(icon){
				var me = this;
				if(me.icon){
					me.iconBox.removeClass(me.icon);
					if(!icon){
						//
						me.el.addClass('c-btn-noicon');
					}
				}
				if(icon){
					me.iconBox.addClass(icon);
					if(!me.icon){
						//
						me.el.removeClass('c-btn-noicon');
					}
				}
				me.icon = icon;
			},
			/**
			 * 
			 */
			active : function(){
				var me = this;
				if(!me.actived){
					me.el.setAttribute('actived', 'actived');
				}
				me.actived = true;
			},
			/**
			 * 
			 */
			unactive : function(){
				var me = this;
				if(me.actived){
					me.el.removeAttribute('actived');
				}
				me.actived = false;
			},
			/**
			 * 
			 */
			enable : function(){
				var me = this;
				if(me.disabled){
					me.disabled = false;
					if(me.el){
						me.el.removeClass('c-btn-disabled');
						me.el.dom.disabled = false;
					}
				}
			},
			/**
			 * 
			 */
			disable : function(){
				var me = this;
				if(!me.disabled){
					me.disabled = true;
					if(me.el){
						me.el.addClass('c-btn-disabled');
						me.el.dom.disabled = true;
					}
				}
			},
			/**
			 * 
			 * @param {String} tooltip 
			 */
			setTooltip : function(tooltip){
				var me = this;
				if(me.el){
					me.el.setAttribute('title', tooltip || '');
				}
				else{
					me.tooltip = tooltip;
				}
			},
			/**
			 * @private
			 * @overwrite
			 */
			initComponent : function(){
				var me = this;
				SP.initComponent.call(me);
				me.tagName = 'button';
				var cls = ['c-btn'];
				if(isS(me.cls)){
					cls.push(me.cls);
				}
				else if(isA(me.cls)){
					cls = cls.concat(me.cls);
				}
				me.cls = cls;
			},
			/**
			 * @private
			 * @overwrite
			 */
			doRender : function(){
				var me = this;
				SP.doRender.call(me);
				me.el.setHideModal('display');
				me.iconBox = me.el.createChild({
					tag : 'i'
				});
				if(me.icon){
					me.iconBox.addClass(me.icon);
				}
				else{
					me.el.addClass('c-btn-noicon');
				}
				me.textBox = me.el.createChild({
					tag : 'b',
					cls : me.textCls || undefined
				});
				
				if(me.text){
					me.textBox.update(me.text);
				}
				else{
					me.el.addClass('c-btn-notext');
				}
				
				if(me.tooltip){
					me.el.setAttribute('title', me.tooltip);
				}
				
				if(isS(me.btnCls) || isA(me.btnCls)){
					me.el.addClass(me.btnCls);
				}
				me.disabled = !!me.disabled;
				if(me.disabled){
					me.el.addClass('c-btn-disabled');
					me.el.dom.disabled = true;
				}
				
				me.actived = !!me.actived;
				if(me.actived){
					me.el.setAttribute('actived', true);
				}
				
				me.el.on('click', me.onClickButton, me);
			},
			/**
			 * 
			 */
			onClickButton : function(){
				var me = this;
				if(!me.disabled && isF(me.handler)){
					me.handler.call(me.scope || me, me);
				}
			}
		});
	}
});