/**
 * @class Cmp.form.DateField
 * @extend Cmp.form.Trigger
 * 
 * 
 * <p>
 * 
 * @cfg {Object} value ()
 * @cfg {String} emptyText ()
 * @cfg {String} label ()
 * @cfg {String} name ()
 * @cfg {Boolean} disabled ()truefalse
 * @cfg {Boolean} hideLabel ()truefalse
 * @cfg {String/Number} labelWidth (): '5rem'
 * @cfg {Boolean} readOnly true
 * @cfg {Date} minValue 1970-01-01
 * @cfg {Date} maxValue 2099-01-01
 * </p>
 */
(function(){
	
	var dateList;
	
	var getDateList = function(listClazz){
		if(dateList){
			return dateList;
		}
		var rel = {
			warp : Cmp.getBody().createChild({cls:'c-datefield-listwarp'}),
			dataList : new listClazz({
				cls : 'c-detelist-tlarrow'
			}),
			mask : Cmp.getBody().createMask('ComboxMask', 900000),
			showed : false
		};
		rel.invokeCallback = function(date){
			if(isF(rel.callback)){
				rel.callback.call(rel.scope, date);
			}
		}
		rel.dataList.on('clickday', rel.invokeCallback, rel);
		rel.warp.setHideModal('display');
		rel.dataList.render(rel.warp);
		rel.warp.hide();
		
		/**
		 * 
		 * @param {Data} date 
		 */
		rel.setValue = function(value, refresh){
			rel.dataList.setValue(value, refresh);
		}
		
		/**
		 * 
		 * @param {Date} min : 197011
		 * @param {Date} max : 20991231
		 */
		rel.setValueRange = function(min, max){
			rel.dataList.setValueRange(min, max);
		}
		
		/**
		 * trueDataList
		 * 
		 */
		rel.isShowed = function(){
			return rel.showed;
		}
		rel.hide = function(){
			var me = this;
			rel.showed = false;
			rel.warp.hide();
			rel.mask.hide();
			rel.mask.un('click', rel.invokeCallback, rel);
		}
		
		/**
		 * 
		 * @param {Array} xy [left,top]
		 * @param {Function} callback 
		 * @param {Object} scope this
		 */
		rel.show = function(xy, callback, scope){
			if(rel.isShowed()){
				return ;
			}
			rel.showed = true;
			rel.warp.setStyle({
				left : xy[0]+'px',
				top : xy[1]+'px'
			});
			rel.warp.show();
			rel.mask.show();
			rel.mask.on('click', rel.invokeCallback, rel);
			rel.callback = callback;
			rel.scope = scope;
		}
		
		dateList = rel;
		return dateList;
	}

	/**
	 * 
	 */
	var toString = function(date){
		var rel = [],v;
		rel.push(date.getFullYear());	
		rel.push('');
		v = date.getMonth()+1;
		if(v < 10){
			rel.push('0');	
		}
		rel.push(v);	
		rel.push('');
		v = date.getDate();
		if(v < 10){
			rel.push('0');	
		}
		rel.push(v);	
		rel.push('');
		
		return rel.join('');
	}
	
	var equals = function(d1, d2){
		if(d1 === d2){
			return true;
		}
		
		return false;
	}
	
	Cmp.define('Cmp.form.DateField',{
		extend : 'Cmp.form.Trigger',
		requires : [
			'Cmp.DateList'
		],
		cls : true,
		factory : function(ext, reqs){
			var superClazz = ext.prototype,
				dateListClazz = reqs[0];
			return Cmp.extend(ext, {
				/**
				 * @public
				 * 
				 * @param {Date} minValue 1970-01-01
				 */
				setMinValue : function(minValue){
					var me = this;
					me.setValueRange(minValue, me.maxValue);
				},
				/**
				 * @public
				 * 
				 * @param {Date} maxValue 2099-01-01
				 */
				setMaxValue : function(maxValue){
					var me = this;
					me.setValueRange(me.minValue, maxValue);
				},
				/**
				 * @public
				 * 
				 * @param {Date} minValue 1970-01-01
				 * @param {Date} maxValue 2099-01-01
				 */
				setValueRange : function(minValue, maxValue){
					var me = this;
					v = minValue;
					if(!isD(v)){
						v = new Date(1970,0,1);
					}
					me.minValue = v;
					v = maxValue;
					if(!isD(v)){
						v = new Date(2099, 11, 31);
					}
					me.maxValue = v;
					
					if(me.minValue.getTime() > me.maxValue.getTime()){
						v = me.minValue;
						me.minValue = me.maxValue;
						me.maxValue = v;
					}
					
					v = me.value; 
					if(isD(v)){
						v = v.getTime(); 
						if(v > me.maxValue.getTime()){
							me.value = me.maxValue;
						}
						else if(v < me.minValue.getTime()){
							me.value = me.minValue;
						}
					}
				},
				/**
				 * @overwrite
				 */
				getValue : function(){
					return this.value;
				},
				/**
				 * @overwrite
				 */
				setValue : function(value){
					var me = this;
					if(isD(value)){
						me.value = value;
					}
					else{
						me.value = undefined;
					}
					if(me.input){
						me.input.dom.value = me.value ? toString(me.value) : '';
					}
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,v;
					me.btnType = 'calendar';
					me.readOnly = true;
					v = me.cls;
					if(isA(v)){
						v.unshift('c-datefield');
					}
					else if(isS(v)){
						v = ['c-datefield', v];
					}
					else{
						v = 'c-datefield';
					}
					me.cls = v;
					
					superClazz.initComponent.call(me);
					v = me.minValue;
					if(!isD(v)){
						v = new Date(1970,0,1);
					}
					me.minValue = v;
					
					v = me.maxValue;
					if(!isD(v)){
						v = new Date(2099, 11, 31);
					}
					me.maxValue = v;
					
					if(me.minValue.getTime() > me.maxValue.getTime()){
						v = me.minValue;
						me.minValue = me.maxValue;
						me.maxValue = v;
					}
					
					me.addEvents(
						/**
						 * @event
						 * 
						 * @param {DateField} this
						 */
						'collect',
						/**
						 * @event
						 * 
						 * @param {DateField} this
						 */
						'extend'
					);
				},
				/**
				 * @private
				 * @overwrite
				 */
				doClickTrigger : function(){
					var me = this;
					if(me.disabled) {
						return;
					}
					if(me.extended){
						me.doCollect();
					}
					else{
						me.doExtend();
					}
				},
				/**
				 * @private
				 */
				doExtend : function(){
					var me = this;
					if(!me.extended){
						me.extended = true;
						
						var list = me.getDateList(),
							h = me.input.getHeight(),
							xy = me.input.getXY();
						list.setValueRange(me.minValue, me.maxValue);	
						list.setValue(me.getValue(), true);
						list.show([xy[0], xy[1]+h], me.doListCallback, me);
						me.el.addClass('c-datefield-extended');
						me.fireEvent('extend', me);
					}	
				},
				/**
				 * @private
				 */
				doListCallback : function(value){
					var me = this,
						ov = me.value;
					if(isD(value)){
						me.value = value;
						me.input.dom.value = toString(value);
					}
					me.doCollect();
					
					if(isD(value) && !equals(ov, value)){
						me.fireEvent('changed', value, ov, me);
					}
				},
				/**
				 * @private
				 */
				doCollect : function(){
					var me = this;
					if(!!me.extended){
						me.extended = false;
						var list = me.getDateList();
						list.hide();
						me.el.removeClass('c-datefield-extended');
						me.fireEvent('collect', me);
					}
				},
				/**
				 * @private
				 */
				getDateList : function(){
					return getDateList(dateListClazz);
				}
			});
		}
	});
}());