/**
 * 
 * @class Cmp.form.CheckboxGroup
 * @extend Cmp.form.Field
 *
 * <p>
 * 
 * @cfg {Array} value ()
 * @cfg {String} text ()
 * @cfg {Array} options ()
 *		{String} value ()
 *		{String} text (),value
 * @cfg {Boolean} disabled ()truefalse
 * @cfg {Boolean} hideLabel ()truefalse
 * @cfg {String/Number} labelWidth (): '5rem'
 * @cfg {Boolean} showAll ()trueCheckboxtrue
 * @cfg {String/Number} optionWidth ()
 * </p>
 *
 * @version 3.0.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){
	Cmp.define('Cmp.form.CheckboxGroup',{
		extend : 'Cmp.form.Field',
		cls : true,
		requires : [
			'Cmp.form.Checkbox'
		],
		factory : function(ext, reqs){
			var superClazz = ext.prototype,
				checkboxClazz = reqs[0];
			return Cmp.extend(ext, {
				/**
				 * 
				 * @param {Array} options 
				 *		{String} value ()
				 *		{String} text (),value
				 */
				setOptions : function(options){
					var me = this,
						ips = me.inputs,
						len,op,vv,tx,
						i, ip;
						
					//
					if(ips){
						for(i in ips){
							ip = ips[i];
							op = Cmp.get(ip.field.el.dom.parentNode);
							ip.field.destroy();
							op.remove();
						}
						me.inputCount = 0;	
						me.inputs = {};
					}
					me.allInput.toUncheckedMode();
					for(i=0, len = isA(options) ? options.length : 0; i<len; i++){
						op = options[i];
						if(isO(op)){
							vv = op.value;
							if(vv == undefined){
								continue;
							}
							tx = op.text;
							if(!tx){
								tx = vv;
							}
						}
						else if(isS(op)){
							tx = vv = op;
						}
						ip = me.appendCheckbox(tx);
						if(ip){
							me.inputs[ip.getId()] = {
								field : ip,
								value : vv,
								checked : false
							};
							me.inputCount++;
						}	
					}
					
					
				},
				/**
				 * 
				 * @return {Array} value 
				 */
				getValue : function(){
					var me = this,
						ops = me.inputs,
						i,op,rel = [];
						
					for(i in ops){
						op = ops[i];
						if(op.checked){
							rel.push(op.value);
						}
					}	
					return rel;
				},
				/**
				 * @public
				 * 
				 */
				setValue : function(vs){
					var me = this,
						ops = me.inputs,
						count = isA(vs) ? vs.length : 0,
						i,op,vv;
					if(me.selectCount === 0 && count === 0){
						return ;
					}
					me.onValueChangeding = true;
					for(i in ops){
						op = ops[i];
						if(count > 0 && vs.indexOf(op.value) > -1){
							op.checked = true;
							op.field.toCheckedMode();
						}
						else{
							op.checked = false;
							op.field.toUncheckedMode();
						}
					}
					me.selectCount = count;	
					if(0 === me.selectCount){
						//
						me.allInput.toUncheckedMode();
					}
					else if(me.inputCount === me.selectCount){
						//
						me.allInput.toCheckedMode();
					}
					else{
						//
						me.allInput.toHalfCheckedMode();
					}
					
					me.onValueChangeding = false;
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,
						cls = me.cls;
					if(isA(cls)){
						cls.unshift('c-checkboxgroup');
					}
					else if(isS(cls)){
						cls = ['c-checkboxgroup', cls];
					}
					else{
						cls = 'c-checkboxgroup';
					}
					me.cls = cls;
					superClazz.initComponent.call(me);
				},
				/**
				 * @private
				 * @overwrite
				 */
				renderInput : function(box){
					var me = this,
						ops = me.options,op,fo,tx,vv,
						i = 0,
						len = isA(ops) ? ops.length : 0;
					
					me.allInputBox = box.createChild({
						cls : 'c-checkboxgroup-allinputbox'
					});
					me.allInput = new checkboxClazz({
						hideLabel : true,
						text : ''
					});
					me.allInput.on('changed', me.onAllSelectChanged, me);
					me.allInput.render(me.allInputBox);
					
					me.optionBox = box.createChild({
						cls : 'c-checkboxgroup-optionbox'
					});
					
					me.inputs = {};
					me.inputCount = 0;
					me.selectCount = 0;
					for(;i<len;i++){
						op = ops[i];
						if(isO(op)){
							vv = op.value;
							if(!vv){
								continue;
							}
							tx = op.text;
							if(!tx){
								tx = vv;
							}
						}
						else if(isS(op)){
							tx = vv = op;
						}
						fo = me.appendCheckbox(tx);
						if(fo){
							me.inputs[fo.getId()] = {
								field : fo,
								value : vv,
								checked : false
							};
							me.inputCount++;
						}
					}
					if(isA(me.value)){
						me.setValue(me.value);
					}
				},
				/**
				 * @private
				 * Checkbox
				 */
				appendCheckbox : function(text){
					var me = this,
						el = me.optionBox,
						box,rel;
					
					box = el.createChild({
						cls : 'c-checkboxgroup-optionitem'
					});
					
					rel = new checkboxClazz({
						hideLabel : true,
						text : text
					});
					rel.on('changed', me.onOptionValueChanged, me);
					rel.render(box);
					return rel;
				},
				/**
				 * @private
				 * 
				 */
				onAllSelectChanged : function(checkModue, field){
					var me = this,
						inputs = me.inputs,
						i,op;
					if(me.onOptionValueChanging
						|| me.onValueChangeding){
						return ;
					}
					me.onAllSelectChanging = true;
					if(field.isCheckeded()){
						me.selectCount = me.inputCount;
						for(i in inputs){
							op = inputs[i];
							op.checked = true;
							op.field.toCheckedMode();
						}
					}	
					else{
						me.selectCount = 0;
						for(i in inputs){
							op = inputs[i];
							op.checked = false;
							op.field.toUncheckedMode();
						}
					}
					me.onAllSelectChanging = false;
				},
				/**
				 * @private
				 * 
				 */
				onOptionValueChanged : function(checkModue, field){
					var me = this,
						inputs = me.inputs,
						cid = field.getId(),
						op = inputs[cid];
					if(me.onAllSelectChanging 
						|| me.onValueChangeding
						|| !op){
						return ;
					}	
					if(field.isCheckeded()){
						op.checked = true;
						me.selectCount++;
					}
					else{
						op.checked = false;
						me.selectCount--;
					}
					me.onOptionValueChanging = true;
					if(0 === me.selectCount){
						//
						me.allInput.toUncheckedMode();
					}
					else if(me.inputCount === me.selectCount){
						//
						me.allInput.toCheckedMode();
					}
					else{
						//
						me.allInput.toHalfCheckedMode();
					}
					me.onOptionValueChanging = false;
				}
			});	
		}
	});
}());