/**
 * @abstract
 * @class Cmp.layout.Layout
 * @extend Object
 * 
 * 
 * @version 2.3.0
 * @since 2016-05-31
 * @author Jinhai
 * 
 */
(function(){

	var Layout = function(cfg){
		var me = this;
		if(isO(cfg)){
			Cmp.apply(me, cfg);
		}
	}
	Layout.prototype = {
		/**
		 * @public
		 * 
		 * @param {Number} index
		 * @return {Widget}
		 */
		getItemAt : function(index){
			var o = this.items[index];
			return o ? o.cmp : undefined;
		},
		/**
		 * @public
		 * 
		 */
		getItemCount : function(){
			var me = this;
			return isA(me.items) ? me.items.length : 0;
		},
		/**
		 * @public
		 * 
		 * Element
		 * relayout
		 * 
		 * @param {Contaner} contaner 
		 */
		layout : function(contaner){
			var me = this,
				box = contaner.getLayoutTarget();
			if(me.contaner){
				//
				return ;
			}
			me.contaner = contaner;
			me.items = [];
			
			me.beforeLayout(box);
			
			var items = contaner.items,
				i=0,len = isA(items) ? items.length : 0;
			for(;i<len;i++){
				me.renderItem(items[i], box, i);
			}
		},
		/**
		 * @public
		 * 
		 * 
		 */
		relayout : function(){
			//TODO
		},
		/**
		 * @public 
		 * 
		 * @param {Object} item
		 * @return {Boolean} true
		 */
		appendItem : function(item){
			//TODO
		},
		/**
		 * @public 
		 * 
		 * @param {Number} index 
		 * @return {Boolean} true
		 */
		removeItem : function(index){
			var me = this,
				it = me.getItemAt(index);
			if(it){
				//
				me.items.splice(index, 1);
				//Dom
				it.cmp.destroy();
				it.el.remove();
				return true;
			}
			
			return false;
		},
		/**
		 * @private
		 * 
		 * @param {Element} target Element
		 */
		beforeLayout : Cmp.emptyFn, 
		/**
		 * @private
		 * 
		 * @param {Object/Widget} item 
		 * @param {Element} box 
		 * @param {Number} index 
		 * @reutrn {Widget} {Widget}
		 */
		renderItem : function(item, box, index){
			var me = this,
				//
				el = box.createChild({
					cls : 'c-ly-item'
				});
			
			me.items[index] = {
				el : el
			};
			
			if(isO(item)){
				if(!isF(item.render)){
					Cmp.create(item, function(cmp){
						if(isO(cmp) && isF(cmp.render)){
							cmp.render(me.items[index].el);
							me.items[index].cmp = cmp;
						}
						else{
							me.items[index].el.setStyle('display', 'none');
						}
					});
				}
				else{
					item.render(me.items[index].el);
					me.items[index].cmp = item;
				}
			}
			else{
				me.items[index].el.setStyle('display', 'none');
			}
		} 
	};
	Cmp.define('Cmp.layout.Layout',{
		factory : function(ext, reqs){
			return Layout;
		}
	});
}());