/**
 * 
 * @class Cmp.form.Checkbox
 * @extend Cmp.form.Field
 *
 * <p>
 * 
 * @cfg {Object} value ()
 * @cfg {String} text ()
 * @cfg {String} label ()
 * @cfg {String} name ()
 * @cfg {Boolean} disabled ()truefalse
 * @cfg {Boolean} hideLabel ()truefalse
 * @cfg {String/Number} labelWidth (): '5rem'
 * @cfg {Boolean/String} checked ()true'true'false'false'
 *				'half'false;
 * </p>
 *
 * @version 3.0.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){
	
	Cmp.define('Cmp.form.Checkbox',{
		extend : 'Cmp.form.Field',
		cls : true,
		factory : function(ext, reqs){
			var superClazz = ext.prototype;
			
			return Cmp.extend(ext, {
				/**
				 * 
				 * @return {String} value 'checked''half'false
				 */
				getValue : function(){
					return this.checkMode;
				},
				/**
				 * 
				 * @param {String} value 'checked''half'false
				 */
				setValue : function(value){
					var me = this;
					if('true' === value || true === value){
						value = 'checked'
					}
					else if('false' === value){
						value = false;
					}
					if(me.checkMode === value){
						return ; 
					}
					if('checked' === value){
						me.toCheckedMode();
					}
					else if('half' === value){
						me.toHalfCheckedMode();
					}
					else{
						me.toUncheckedMode();
					}
				},
				/**
				 * true;
				 * 
				 * @return {Boolean}
				 */
				isCheckeded : function(){
					return 'checked' === this.checkMode;
				},
				/**
				 * true
				 * @return {Boolean}
				 */
				isHalfChecked : function(){
					return 'half' === this.checkMode;
				},
				/**
				 * true
				 * 
				 * @return {Boolean}
				 */
				isUnchecked : function(){
					return false === this.checkMode;
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,
						cls = me.cls;
					if(isA(cls)){
						cls.unshift('c-checkbox');
					}
					else if(isS(cls)){
						cls = ['c-checkbox', cls];
					}
					else{
						cls = 'c-checkbox';
					}
					me.cls = cls;
					superClazz.initComponent.call(me);
					cls = me.checked;
					if('half' === cls){
						cls = 'half';
					}
					else if(true === cls || 'true' === cls){
						cls = 'checked';
					}
					else{
						cls = false;
					}
					me.checkMode = cls;
					delete me.checked;
					
					me.addEvents(
						/**
						 * 
						 * false
						 *
						 * @param {String} checkmode 'checked''half'false
						 * @param {String} afterCheckmode 'checked''half'false
						 * @param {Checkbox} this
						 */
						'click'
					);
				},
				/**
				 * @private
				 * @overwrite
				 */
				renderInput : function(box){
					//text
					var me = this,dom;
					me.warp = box.createChild({
						cls : 'c-checkbox-warp',
						html : '<div class="c-checkbox-input"></div><div class="c-checkbox-text"></div>'
					});
					dom = me.warp.dom;
					me.checkbox = Cmp.get(dom.firstChild);
					me.textBox = Cmp.get(dom.childNodes[1]);
					
					if(isS(me.text)){
						me.textBox.update(me.text);
					}
					if(false !== me.checkMode){
						me.warp.addClass('c-checkbox-'+me.checkMode);
						if('half' === me.checkMode){
							me.checkbox.update('<span class="c-checkbox-half-outer"></span><span class="c-checkbox-half-inner"></span>');
						}
					}
					me.checkbox.on('click', me.onClickInput, me);
				},
				/**
				 * @private
				 */
				onClickInput : function(){
					var me = this;
					if(me.isDisabled()){
						return ; 
					}
					if(false !== me.fireEvent('click', me.checkMode, (me.isCheckeded() ? false:'checked'),me)){
						//
						if(me.isCheckeded()){
							me.toUncheckedMode();
						}
						else{
							me.toCheckedMode();
						}
					
					}
				},
				/**
				 * @public
				 * 'changed'
				 */
				toCheckedMode : function(){
					var me = this;
					if(me.isCheckeded()){
						return me;
					}
					if(false !== me.checkMode){
						me.warp.removeClass('c-checkbox-'+me.checkMode);
						me.checkbox.update('');
					}
					
					me.checkMode = 'checked';
					me.warp.addClass('c-checkbox-'+me.checkMode);
					
					me.fireEvent('changed', me.checkMode, me);
					return me;
				},
				/**
				 * @public
				 * 'changed'
				 */
				toHalfCheckedMode : function(){
					var me = this;
					if(me.isHalfChecked()){
						return me;
					}
					if(false !== me.checkMode){
						me.warp.removeClass('c-checkbox-'+me.checkMode);
					}
					me.checkMode = 'half';
					me.warp.addClass('c-checkbox-'+me.checkMode);
					me.checkbox.update('<span class="c-checkbox-half-outer"></span><span class="c-checkbox-half-inner"></span>');
					me.fireEvent('changed', me.checkMode, me);
					return me;
				},
				/**
				 * @public
				 * 'changed'
				 */
				toUncheckedMode : function(){
					var me = this;
					if(me.isUnchecked()){
						return me;
					}
					me.warp.removeClass('c-checkbox-'+me.checkMode);
					me.checkbox.update('');
					me.checkMode = false;
					me.fireEvent('changed', me.checkMode, me);
					return me;
				}
			}); 
		}
	});
}());