/**
 * @class Cmp.DateList
 * @extend Cmp.Widget
 * 
 * 
 * <p>
 * 
 * @cfg {Date} value  
 * @cfg {Date} minValue : 197011
 * @cfg {Date} maxValue : 20991231
 * </p>
 * 
 * @version 1.0.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){

	var DATALIST_BOX_HTML = [],
		MONTH_TEXT = [
			'','','','',
			'','','','',
			'','','',''
		];
	
	/**
	 * 
	 * @param {Number} year ()2016
	 * @param {Number} month ()0~11
	 */	
	var getDaysOfMonth = function(year, month){
		switch(month){
			//
			case 3:
			//
			case 5:
			//
			case 8:
			//
			case 10:
				return 30;
			case 1:
				if(0 === year % 400 || (0 !== year % 100 && 0 === year % 4)){
					//
					return 29;
				}
				else{
					//
					return 28;
				}
				
			default :
				return 31	
		}
	}
	
	var buildDayRow = function(rowClss){
		var html = [];
		html.push('<div class="c-datelist-row ');
		html.push(rowClss);
		html.push('">');
		html.push('<div class="c-datelist-cell"><span class="c-datelist-cell-inner c-datelist-sun"></span></div>');
		html.push('<div class="c-datelist-cell"><span class="c-datelist-cell-inner c-datelist-mon"></span></div>');
		html.push('<div class="c-datelist-cell"><span class="c-datelist-cell-inner c-datelist-tue"></span></div>');
		html.push('<div class="c-datelist-cell"><span class="c-datelist-cell-inner c-datelist-wed"></span></div>');
		html.push('<div class="c-datelist-cell"><span class="c-datelist-cell-inner c-datelist-thu"></span></div>');
		html.push('<div class="c-datelist-cell"><span class="c-datelist-cell-inner c-datelist-fri"></span></div>');
		html.push('<div class="c-datelist-cell"><span class="c-datelist-cell-inner c-datelist-sat"></span></div>');
		
		html.push('</div>');
		return html.join('');
	}
	
	var html = [];	
	//HEAD Start
	html.push('<div class="c-datelist-head">');
	html.push('<div class="c-datelist-head-left">');
	html.push('<div class="c-datelist-headbtn fa fa-fast-backward" title=""></div>');
	html.push('<div class="c-datelist-headbtn fa fa-step-backward" title=""></div>');
	html.push('</div>');
	html.push('<div class="c-datelist-head-center"></div>');
	html.push('<div class="c-datelist-head-right">');
	html.push('<div class="c-datelist-headbtn fa fa-fast-forward" title=""></div>');
	html.push('<div class="c-datelist-headbtn fa fa-step-forward" title=""></div>');
	html.push('</div></div>');
	//HEAD End
	//BODY Start
	html.push('<div class="c-datelist-body">');
	//
	html.push(buildDayRow('c-datelist-week'));
	//7
	for(var i=0; i<6;i++){
		html.push(buildDayRow('c-datelist-day'));
	}
	html.push('</div>');
	//BODY End
	DATALIST_BOX_HTML = html.join('');
	
	/**
	 * 
	 */
	var DayButtom = function(dom, handler, scope){
		var me = this;
		me.el = Cmp.get(dom);
		me.textEl = Cmp.get(dom.firstChild);
		me.disabled = false;
		me.selected = false;
		me.currentMonth = true;
		me.handler = handler;
		me.scope = scope;
		
		me.el.on('click', me.onClick, me);
	};
	
	DayButtom.prototype = {
		/**
		 * 
		 * @param {Array} day [year,month,dayOfMonth]
		 * @param {Boolean} noCurrentMonth true
		 * @param {Array} nowDay [year,month,dayOfMonth]
		 */
		setValue : function(day, noCurrentMonth, nowDay){
			var me = this,
				t = day[2];
				
			if(t < 10){
				t = '0'+t;
			}	
			me.textEl.update(t);
			me.value = day;
			if(!!noCurrentMonth && true === me.currentMonth){
				me.el.addClass('c-datelist-nocurrent');
				me.currentMonth = false;
			}
			else if(!noCurrentMonth && false === me.currentMonth){
				me.el.removeClass('c-datelist-nocurrent');
				me.currentMonth = true;
			}
			
			if(day[0] === nowDay[0]
				&& day[1] === nowDay[1]
				&& day[2] === nowDay[2]){
				me.el.addClass('c-datelist-today');
			}
			else{
				me.el.removeClass('c-datelist-today');
			}
		},
		/**
		 * true
		 */
		isSelected : function(){
			return !!this.selected;
		},
		/**
		 * 
		 */
		select : function(){
			var me = this;
			if(!me.isSelected()){
				me.el.addClass('c-datelist-selected');
				me.selected = true;
			}
		
		},
		/**
		 * 
		 */
		unselect : function(){
			var me = this;
			if(me.isSelected()){
				me.el.removeClass('c-datelist-selected');
				me.selected = false;
			}
		},
		/**
		 * true
		 */
		isDisabled : function(){
			return !!this.disabled;
		},
		/**
		 * 
		 */
		disable : function(){
			var me = this;
			if(!me.isDisabled()){
				me.el.addClass('c-datelist-btn-disabled');
				me.el.setAttribute('disabled', true);
				me.disabled = true;
			}
		},
		/**
		 * 
		 */
		enable : function(){
			var me = this;
			if(me.isDisabled()){
				me.el.removeClass('c-datelist-btn-disabled');
				me.el.removeAttribute('disabled');
				me.disabled = false;
			}
		},
		/**
		 * @private
		 */
		onClick : function(){
			var me = this;
			if(!me.isDisabled() && isF(me.handler)){
				me.handler.call(me.scope, me.value, me);
			}
		}
	};
	
	/**
	 * d1d20
	 * @param {Array} d1 [year,month,day]
	 * @param {Array} d2 [year,month,day]
	 */
	var compareOfDay = function(d1, d2){
		for(var i=0,v;i<3;i++){
			v = d1[i] - d2[i];
			if(v != 0){
				return v;
			}	
		}
		return 0;
	}

/**
	 * DomButton
	 * @param {HTMLElement} dom
	 * @param {Function} handler 
	 * @param {Object} value 
	 */
	var buildBtnObject = function(dom, handler, value){
		var me = this,
			rel = {
				el : Cmp.get(dom),
				disabled : false,
				handler : handler,
				scope : me,
				value : value
			};
		
		rel.el.on('click', function(){
			if(false === rel.disabled){
				rel.handler.call(rel.scope || me, rel.value);
			}
		});	
		
		rel.disable = function(){
			if(false === rel.disabled){
				rel.el.addClass('c-datelist-headbtn-disabled');
				rel.disabled = true;
			}
		}
		rel.enable = function(){
			if(true === rel.disabled){
				rel.el.removeClass('c-datelist-headbtn-disabled');
				rel.disabled = false;
			}
		}
		
		return rel;
	}
	
	/** 
	 * 
	 */
	var buildDayBtn = function(dom){
		var me = this;
		return new DayButtom(dom, me.onClickDayBtn, me);
	}
	
	/**
	 * 
	 * @param {Number} year ()2016
	 * @param {Number} month ()0~11
	 */
	var refreshCmps = function(year, month){
		var me = this,
			cmps = me.cmps,
			dayBtns = cmps.dayBtns,
			minDay = [me.minYear,me.minMonth,me.minDay],
			maxDay = [me.maxYear,me.maxMonth,me.maxDay],
			nowDay = new Date();
		nowDay = [
			nowDay.getFullYear(),
			nowDay.getMonth(),
			nowDay.getDate()
		];	
		var setSelectedStatus = function(btn, dv){
			if(!me.value){
				btn.unselect();
			}
			else{
				var y = me.value.getFullYear(),
					m = me.value.getMonth(),
					d = me.value.getDate();
				
				if(dv[0] === y && dv[1] === m && dv[2] === d){
					btn.select();
				}	
				else{
					btn.unselect();
				}
			}
		}
		var setDisabledStatus = function(btn, dv){
			var y = dv[0],m=dv[1],d = dv[2];
			if(compareOfDay(minDay, dv) < 1 
				&& compareOfDay(maxDay, dv) > -1){
				btn.enable();
			}
			else{
				btn.disable();
			}
		}
		
		
		//
		if(me.minYear === year){
			cmps.forwardYearBtn.disable();
			if(me.minMonth >= month){
				cmps.forwardMonthBtn.disable();
			}
			else{
				cmps.forwardMonthBtn.enable();
			}
		}
		else if(me.minYear > year){
			cmps.forwardYearBtn.disable();
			cmps.forwardMonthBtn.disable();
		}
		else{
			cmps.forwardYearBtn.enable();
			cmps.forwardMonthBtn.enable();
		}
		
		//
		if(me.maxYear > year){
			cmps.nextYearBtn.enable();
			cmps.nextMonthBtn.enable();
		}
		else if(me.maxYear === year){
			cmps.nextYearBtn.disable();
			if(me.maxMonth <= month){
				cmps.nextMonthBtn.disable();
			}
			else{
				cmps.nextMonthBtn.enable();
			}
		}
		else{
			cmps.nextYearBtn.disable();
			cmps.nextMonthBtn.disable();
		}
		
		//
		cmps.currentMonthBox.update(year+','+(month+1)+'');
		
		//		
		var monthFirstDay = new Date(year, month, 1), //
		 	dayOfWeek = monthFirstDay.getDay(),//;
		 	//
		 	//
		 	ly,lm,
		 	startDay, //
		 	//
		 	mds,
		 	ix,i,len,di,d;
		 
		 dayOfWeek = dayOfWeek == 0 ? 7 : dayOfWeek;
		 	
		 //
		 if(month > 0){
		 	ly = year,
		 	lm = month-1;
		 }
		 else{
		 	ly = year-1;
		 	lm = 11;
		 }
		 
		 //
		 mds = getDaysOfMonth(ly, lm);	
		 startDay = mds - dayOfWeek;
		 for(i=0,len = dayOfWeek; i < len; i++){
		 	di = dayBtns[i];
		 	d = [ly,lm,startDay+1+i];
		 	di.setValue(d, true, nowDay);
		 	setSelectedStatus(di, d);
		 	setDisabledStatus(di, d);
		 }
		 
		 //
		 mds = getDaysOfMonth(year, month);	
		 for(i=0, len = mds; i<len;i++){
		 	ix = dayOfWeek+i;
		 	di = dayBtns[ix];
		 	d = [year, month,i+1];
		 	di.setValue(d, false, nowDay);
		 	setSelectedStatus(di, d);
		 	setDisabledStatus(di, d);
		 }
		 
		 //
		 if(month == 11){
		 	ly = year+1,lm = 0;
		 }
		 else{
		 	ly = year,lm = month+1;
		 }
		 len = mds+dayOfWeek;
		 if(dayBtns.length > len){
		 	i = len, len = dayBtns.length,ix=1;
		 	for(;i<len;i++,ix++){
		 		di = dayBtns[i];
		 		d = [ly,lm,ix];
		 		di.setValue(d, true, nowDay);
		 		setSelectedStatus(di, d);
			 	setDisabledStatus(di, d);
		 	}
		 }
	}//End refreshCmps Function		

	Cmp.define('Cmp.DateList',{
		extend : 'Cmp.Widget',
		cls : true,
		factory : function(ext, reqs){
			var superClazz = ext.prototype;
			
			return Cmp.extend(ext, {
				/**
				 * 
				 * @param {Data} date 
				 * @param {Boolean} refresh true;
				 */
				setValue : function(date, refresh){
					var me = this;
					if(me.value === date && !refresh){
						return ;
					}
					me.value = date;
					if(me.cmps){
						var y,m;
						if(isD(date)){
							y = date.getFullYear();
							m = date.getMonth();
						}
						else{
							date = new Date();
							y = date.getFullYear();
							m = date.getMonth();
						}
						me.currentYear = y;
						me.currentMonth = m;
						
						refreshCmps.call(me, y, m);
					}
				},
				/**
				 * @public
				 * 
				 * @param {Date} min : 197011
				 * @param {Date} max : 20991231
				 */
				setValueRange : function(min, max){
					var me = this,v;
					if(isD(min) && isD(max)){
						if(min.getTime() > max.getTime()){
							v = min;
							min = max;
							max = v;
						}
					}
					if(!isD(min)){
						me.minYear = 1970;
						me.minMonth = 0;
						me.minDay = 1;
					}
					else{
						me.minYear = min.getFullYear();
						me.minMonth = min.getMonth();
						me.minDay = min.getDate();
					}
					if(!isD(max)){
						me.maxYear = 2099;
						me.maxMonth = 11;
						me.maxDay = 31;
					}
					else{
						me.maxYear = max.getFullYear();
						me.maxMonth = max.getMonth();
						me.maxDay = max.getDate();
					}
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,v = me.cls;
					
					if(isA(v)){
						v.unshift('c-datelist');
					}
					else if(isS(v)){
						v = ['c-datelist', v];
					}
					else{
						v = 'c-datelist';
					}
					me.cls = v;
					superClazz.initComponent.call(me);
					
					me.setValueRange(me.minValue, me.maxValue);
					delete me.minValue;
					delete me.maxValue;
					
					me.addEvents(
						/**
						 * @event
						 * 
						 * @param {Date} day 
						 * @return {Boolean} false'selectedchanged'
						 */
						'clickday',
						/**
						 * @event
						 * 
						 * @param {Date} day 
						 */
						'selectedchanged'
					);
				},
				/**
				 * @private
				 * @overwrite
				 */
				doRender : function(){
					var me = this,
						box,dom,subDoms,el,
						cmps = {};
					superClazz.doRender.call(me);	
					
					el = me.el.createChild({
						cls : 'c-datelist-box',
						html : DATALIST_BOX_HTML
					});
					me.el.createChild({
						cls : 'c-datelist-arrow'
					});
					dom = el.dom.firstChild;
					subDoms = dom.childNodes;
					cmps.forwardYearBtn = buildBtnObject.call(me, subDoms[0].firstChild, me.onClickForwardYear);
					cmps.forwardMonthBtn = buildBtnObject.call(me, subDoms[0].childNodes[1],me.onClickForwardMonth);
					
					cmps.currentMonthBox = Cmp.get(subDoms[1]);
					cmps.nextYearBtn = buildBtnObject.call(me, subDoms[2].firstChild, me.onClickNextYear);
					cmps.nextMonthBtn = buildBtnObject.call(me, subDoms[2].childNodes[1], me.onClickNextMonth);
					
					
					//
					dom = el.dom.childNodes[1];
					subDoms = dom.childNodes;
					var i=1,j,sd,dayBtns = [],row;
					for(;i<7;i++){
						row = subDoms[i];
						sd = row.childNodes;
						for(j=0;j<7;j++){
							dayBtns.push(buildDayBtn.call(me, sd[j]));
						}
					}
					cmps.dayBtns = dayBtns;
					me.cmps = cmps;
					var v = me.value;
					me.value = false;
					me.setValue(v);
				},
				/**
				 * @private
				 * 
				 */
				onClickForwardYear : function(){
					var me = this,
						y = me.currentYear-1,
						m = me.currentMonth;
						
					if(me.minYear === y && me.minMonth > m){
						m = me.minMonth;
					} 	
						
					me.currentYear = y;
					me.currentMonth = m;
					refreshCmps.call(me, y, m);
				},
				/**
				 * @private
				 * 
				 */
				onClickForwardMonth : function(){
					var me = this,
						y,m;
					
					if(0 === me.currentMonth){
						m = 11;
						y = me.currentYear-1;
					}
					else{
						m = me.currentMonth-1;
						y = me.currentYear;
					}
					
					me.currentYear = y;
					me.currentMonth = m;
					refreshCmps.call(me, y, m);
				},
				/**
				 * @private
				 * 
				 */
				onClickNextYear : function(){
					var me = this,
						y = me.currentYear+1,
						m = me.currentMonth;
						
					
					if(me.maxYear === y && me.maxMonth < m){
						m = me.maxMonth;
					}	
						
					me.currentYear = y;
					me.currentMonth = m;
					refreshCmps.call(me, y, m);
				},
				/**
				 * @private
				 * 
				 */
				onClickNextMonth : function(){
					var me = this,
						y,m;
					
					if(11 === me.currentMonth){
						m = 0;
						y = me.currentYear+1;
					}
					else{
						m = me.currentMonth+1;
						y = me.currentYear;
					}
					
					me.currentYear = y;
					me.currentMonth = m;
					refreshCmps.call(me, y, m);
				},
				/**
				 * @private
				 * 
				 * @param {Array} [year,month,day]
				 */
				doChangeValue : function(day){
					var me = this,
						y = day[0],
						m = day[1]
						d = new Date(y, m, day[2]);
					//'clickday'
					if(false !== me.fireEvent('clickday', d, me)){
						if(me.value && me.value.getFullYear() === y 
								&& me.value.getMonth() === m
								&& me.value.getDate() === day[2]){
								//
						}
						else{
							me.value = d;
							refreshCmps.call(me, y, m);
							me.fireEvent('selectedchanged', d);
						}
					}	
				},
				/**
				 * @private
				 * 
				 */
				onClickDayBtn : function(day, btn){
					var me = this,
						y = day[0],
						m = day[1];
					
					if(y === me.currentYear && m === me.currentMonth){
						// 
						me.doChangeValue(day);
					}	
					else if(y === me.currentYear){
						//
						if(m > me.currentMonth){
							me.onClickNextMonth();
						}
						else{
							me.onClickForwardMonth();
						}
					}
					else{
						//
						if(y > me.currentYear){
							me.onClickNextMonth();
						}
						else{
							me.onClickForwardMonth();
						}
					}
					
				}
			});
		}
	});		

}());