/**
 * 
 * 
 * @class Cmp.grid.GridPanel
 * @extend Cmp.Componet
 * 
 * @version 2.0.0
 * @since 2016-03-31
 * @author Jinhai
 */
(function(){

	Cmp.define('Cmp.grid.GridPanel',{
		extend : 'Cmp.Widget',
		requires : [
			'Cmp.grid.GridView',
			'Cmp.grid.RowSelectionModel',
			'Cmp.grid.ColumnModel',
			'Cmp.grid.RowModel'
		],
		cls : true,
		factory : function(ext, reqs){
			var superClass = ext.prototype,
				viewClass = reqs[0],
				selModelClass = reqs[1],
				colModelClass = reqs[2],
				rowModelClass = reqs[3];
			return Cmp.extend(ext, {
				/**
				 * @cfg {TableModel} data  
				 */
				
				/**
				 * @cfg {Array} columns 
				 */ 
			
				/**
				 * @cfg {Boolean} hideHeader ()truefalse
				 */
			
				/**
				 * @cfg {Widget/Object} footer ()false
				 *	PagingBarPagingBar
				 *	Widget
				 */
				
				/**
				 * @cfg {Boolean} striped truefalse
				 */
				
				/**
				 * @cfg {Function} getRowClass
				 * CSSnullCSS
				 * striped 
				 * @param {Record} record 
				 * @param {Number} rowIndex 
				 */
				 
				/**
				 * 
				 */
				getView : function(){
					var me = this;
					if(!me.view){
						me.view = new viewClass();
					}
					return me.view;
				},
				/**
				 * 
				 */
				getSelectionModel : function(){
					return this.selModel;
				},				 
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this;
					superClass.initComponent.call(me);
					var cls = ['c-grid'];
					if(isS(me.cls)){
						cls.push(me.cls);
					}
					else if(isA(me.cls)){
						cls = cls.concat(me.cls);
					}
					me.cls = cls;
					me.getColumnModel();
					me.getRowModel();
					if(me.sm || me.selModel){
						me.selModel = me.selModel || me.sm;
						if(isO(me.selModel)){
							if(!isF(me.selModel.setSelections)){
								me.selModel = new selModelClass(me.selModel);
							}
						}
						else{
							me.selModel = new selModelClass();
						}
					}
					else{
						me.selModel = new selModelClass({noselect : true});
					}
				},
				/**
				 * 
				 */
				getColumnModel : function(){
					var me = this;
					if(!me.columnModel){
						me.columnModel = new colModelClass(me.columns);
					}
					return me.columnModel;
				},
				/**
				 * 
				 */
				getRowModel : function(){
					var me = this;
					if(!me.rowModel){
						me.rowModel = new rowModelClass(me);
					}
					return me.rowModel;
				},
				/**
				 * @private
				 * @overwrite
				 */
				doRender : function(){
					var me = this;
					superClass.doRender.call(me);
					var view = me.getView();
					view.render(me);
				}
			});	
		}
	});
}());