/**
 * @class Cmp.util.KeyMap
 * @extend Object
 * 
 * 
 * @version 2.0.0
 * @since 2016-03-31
 * @author Jinhai
 */
(function(){

	/**
	 * 
	 * @param {Element} target 
	 * @param {String} keyEventName 'keypress','keydown','keyup'
	 */
	var Rel = function(target, keyEventName){
		var me = this,
			//
			prevented = false,
			//
			bingedKeyEvent = false;
		me.keyMaps = {};
		me.eventProxy = new Cmp.util.EventProxy();
		
		/**
		 * @private
		 * 
		 */
		var onKeyEvent = function(ev){
			var c = ev.keyCode;
			c = c ? c+'' : false;
//			putLog('KeyMap#onKeyEvent> keyCode:'+c);
			c = me.keyMaps[c];
			if(c){
				if(isF(c.handler)){
					var args = [ev, target];
					if(isA(c.args)){
						args = args.concat(c.args);
					}
					if(false !== c.handler.apply(c.scope || window, args)){
						me.eventProxy.fire(ev, target);
					}
				}
				else{
					me.eventProxy.fire(ev, target);
				}
			}
			else if(!prevented){
				me.eventProxy.fire(ev, target);
			}
			else{
				ev.preventDefault();
			}
		}
		
		/**
		 *  
		 */
		me.bindKeyEvent = function(){
			if(!bingedKeyEvent){
				bingedKeyEvent = true;
				target.on(keyEventName, onKeyEvent, me);
			}
		}
		/**
		 * 
		 */
		me.releaseKeyEvent = function(){
			if(bingedKeyEvent){
				bingedKeyEvent = false;
				target.un(keyEventName, onKeyEvent, me);
			}
		}
		
		/**
		 * 
		 * (JavaScriptEvent#stopPropagation())(JavaScriptEvent#preventDefault())
		 */
		me.preventKeyEvent = function(){
			prevented = true;
		}
		
		/**
		 * 
		 */
		me.unpreventKeyEvent = function(){
			prevented = false;
		}
		
		/**
		 * 
		 */
		me.distory = function(){
			me.releaseKeyEvent();
			
			me.eventProxy.destory();

			delete me.keyMaps;
			delete me.eventProxy;
		}
	};
	
	Rel.prototype = {
	
		/**
		 * 
		 * eventDom
		 * @param {Fucntion} handler 
		 * @param {Object} scope this
		 */
		addListener : function(handler, scope){
			if(isF(handler)){
				this.eventProxy.addListener(handler, scope);
			}
		},
		/**
		 * 
		 * @param {Fucntion} handler 
		 * @param {Object} scope this
		 */
		removeListener : function(handler, scope){
			if(isF(handler)){
				this.eventProxy.removeListener(handler, scope);
			}
		},
		/**
		 * 
		 *
		 * @param {String} keyCode ()
		 * @param {Fucntion} handler ()eventDom
		 *		false#addListener()
		 * @param {Object} scope ()handlerthis
		 * @param {Array} args ()handler
		 */
		addKey : function(keyCode, handler, scope, args){
			var me = this;
			if(isO(keyCode)){
				scope = keyCode.scope;
				args = keyCode.args;
				var	i,v;
				for(i in keyCode){
					if('scope' === i
						|| 'args' === i){
						continue;
					}
					v = keyCode[i];
					i = i+'';
					me.keyMaps[i] = {
						handler : v,
						scope : scope,
						args : args
					};
				}
			}
			else if(isN(keyCode) || isS(keyCode)){
				keyCode = keyCode + '';
				me.keyMaps[keyCode] = {
					handler : handler,
					scope : scope,
					args : args
				};
			}
		},
		/**
		 * 
		 * @param {String/Number/Array} keyCode ()
		 */
		removeKey : function(keyCode){
			var me = this;
			
			if(isA(keyCode)){
				for(var i=0,len = keyCode.length, n; i<len;i++){
					n = keyCode[i]+'';
					delete me.keyMaps[n];
				}
			}
			else if(isN(keyCode) || isS(keyCode)){
				keyCode = keyCode+'';
				delete me.keyMaps[keyCode];
			}
		}
	};
	Rel.prototype.on = Rel.prototype.addListener;
	Rel.prototype.un = Rel.prototype.removeListener;
	
	Cmp.define('Cmp.util.KeyMap',{
		factory : function(){
			return Rel;
		}
	});
}());