/**
 * 
 * <p>
 * Javajava.util.MapJavaScriptJS
 * 
 * @class Cmp.util.Record
 * @extend Cmp.util.Observable
 *
 * @version 1.0.0
 * @since 2015-10-31
 * @author Jinhai
 */
(function(){
	var Record = Cmp.extend(Cmp.util.Observable, {
		/**
		 * @constructor
		 * 
		 * @data {Object} value 
		 * @data {Array} allowKeies 
		 *		(nullundefined0)key
		 *
		 * @param {Object} config @data
		 * @param {Mixed} listeners 
		 */
		constructor : function(config, listeners){
			var me = this,
				//
				data = {},
				//key
				keies = [],
				//key
				allowKeies = false,
				//
				length = 0,
				//
				editing = false,
				editBatch;
			
			me.addEvents(
				/**
				 * @event
				 * 
				 * <p>
				 * setremove
				 * endEdit,commit
				 *
				 * @param {Record} this
				 */
				'changed'
			);
			//
			Record.superclass.constructor.call(me, {
				listeners : listeners
			});
			
			/**
			 * put
			 * @param {String/Object/Record} key 
			 *									 Record
			 *									 key/value
			 * @param {Object} value undefined(key)
			 * @return {Boolean} truefalse.
			 */
			var putByInner = function(key, value){
				var k,v,i,ov,c = false;
				if(isS(key)){
					ov = {};
					ov[key] = value;
					key = ov;
				}
				for(k in key){
					v = key[k];
					ov = data[k];
					if(v !== ov
							&& (!allowKeies || allowKeies.indexOf(k) > -1)){
						i = keies.indexOf(k);
						if(undefined === v){
							delete data[k];
							if(i > -1){
								keies.slice(i, 1);
								length--;
							}
						}
						else{
							data[k] = v;
							if(i < 0){
								keies.push(k);
								length++;
							}
						}
						c = true;
					}
				}
				
				return c;
			}
			/**
			 * remove
			 * 
			 * @param {String/Array} key 
			 * @return {Object/Array} key
			 *		undefined
			 */
			var removeByInner = function(key){
				var ind,k,ov,c,rel;
				if(isS(key)){
					ov = data[key];
					delete data[key];
					if(undefined === ov){
						return undefined;
					}
					ind = keies.indexOf(key);
					if(ind > -1){
						keies.slice(ind, 1);
						length--;
					}
					return ov;
				}
				else if(isA(key)){
					rel = [];
					c = false;
					for(var i=0, len = key.length; i<len; i++){
						k = key[i];
						ov = data[k];
						delete data[key];
						if(undefined === ov){
							rel.push(undefined);
						}
						else{
							rel.push(ov);
							c = true;
							ind = keies.indexOf(k);
							if(ind > -1){
								keies.slice(ind, 1);
								length--;
							}
						}
					}
				}
				return c ? rel : undefined;
			}
			
			/**
			 * truecount0
			 *
			 * @return {Boolean} true
			 */
			me.isEmpty = function(){
				return 0 === length;
			}
			/**
			 * key
			 *
			 * @param {String} key 
			 * @return {Object} 
			 */
			me.get = function(key){
				return data[key];
			}
			/**
			 * 
			 * 
			 * @param {String/Object/Record} key 
			 *									 Record
			 *									 key/value
			 * @param {Object} value undefined(key)
			 * @return {Record} this
			 */
			me.put = function(key, value){
				if(editing){
					editBatch.push({
						inkove : 'putByInner',
						params : [key, value]
					})
				}
				else{
					var c = putByInner(key, value);
					if(c){
						me.fireEvent('changed', me);	
					}
				}
				return me;
			}
			/**
			 * 
			 * <p>
			 * 
			 * @param {String/Array} key 
			 * @return {Object/Array} keyundefined
			 */
			me.remove = function(key){
				if(editing){
					editBatch.push({
						inkove : 'removeByInner',
						params : [key]
					});
					return undefined;
				}
				else{
					var c = removeByInner(key);
					if(c){
						me.fireEvent('changed', me);
					}
					return c;
				}
			}
			/**
			 * 
			 */
			me.count = function(){
				return length;
			}
			/**
			 * 
			 * @return {Array} 
			 */
			me.keies = function(){
				return keies.slice(0);
			}
			/**
			 * 
			 * @return {Array} 
			 */
			me.values = function(){
				var i,rel = [];
				for(i in data){
					rel.push(data[i]);
				}
				return rel;
			},
			/**
			 * keyvalue
			 *
			 * @return {Array} keyvalue
			 */
			me.entrys = function(){
				var i, rel = [];
				for(i in data){
					rel.push({
						key : i,
						value : data[i]
					});
				}
			} 
			/**
			 * 
			 *
			 * @return {Object} 
			 */
			me.data = function(){
				return Cmp.apply({}, data);
			}
			/**
			 * 
			 * <p>
			 * putremove'changed'
			 * commitendEdit
			 */
			me.beginEdit = function(){
				if(false === editing){
					editBatch = [];
				}
				editing = true;
				return me;
			}
			
			/**
			 * 
			 */
			me.cancelEdit = function(){
				editBatch = undefined;
				editing = false;
				return me;
			}
			/**
			 * 
			 *
			 * @return {Boolean} true
			 */
			me.commit = function(){
				if(true === editing){
					var i = 0,
						batchs = editBatch.splice(0),
						len = batchs.length,
						b,c = false;
					
					for(;i<len;i++){
						b = batchs[i];
						if('removeByInner' === b.inkove){
							if(undefined !== removeByInner(b.params[0])){
								c = true;
							}
						}	
						else if('putByInner' === b.inkove){
							if(true === putByInner(b.params[0],b.params[1])){
								c = true;
							}
						}
					}
					if(c){
						me.fireEvent('changed', me);
						return true;
					}
				}
				
				return false;
			}
			/**
			 * 
			 */
			me.endEdit = function(){
				me.commit();
				me.cancelEdit();
			}
			
			
			//
			if(isA(config.allowKeies) && config.allowKeies.length > 0){
				allowKeies = config.allowKeies.slice(0);
			}
			
			if(isO(config.value)){
				for(var k in config.value){
					if(!allowKeies || allowKeies.indexOf(k) > -1){
						data[k] = config.value[k];
						keies.push(k);
					}
				}
				length = keies.length;
			}
		}
	});
	
		
	Cmp.define('Cmp.util.Record',{
		factory : function(){
			return Record;
		}
	});
}());