/**
 * 
 * @class Cmp.form.TextField
 * @extend Cmp.form.Field
 *
 * <p>
 * 
 * @cfg {Object} value ()
 * @cfg {String} emptyText ()
 * @cfg {String} label ()
 * @cfg {String} name ()
 * @cfg {Boolean} disabled ()truefalse
 * @cfg {Boolean} hideLabel ()truefalse
 * @cfg {String/Number} labelWidth (): '5rem'
 * @cfg {Boolean} readOnly true
 * </p>
 *
 * @version 3.0.0
 * @since 2016-05-31
 * @author Jinhai
 */
(function(){
	
	Cmp.define('Cmp.form.TextField',{
		extend : 'Cmp.form.Field',
		cls : true,
		
		factory : function(ext, reqs){
			var superClazz = ext.prototype;
			
			return Cmp.extend(ext, {
				/**
				 * 
				 * @return {String} value 
				 */
				getValue : function(){
					var me = this;
					if(me.input){
						return me.input.dom.value;
					}
					else{
						return me.value;
					}
				},
				setValue : function(value){
					var me = this;
					if(me.input){
						me.input.dom.value = value || '';
					}
				},
				/**
				 *  readOnly
				 */
				setReadOnly : function(readOnly){
					var me = this;
					readOnly = !!readOnly;
					if(me.input){
						if(readOnly){
							me.input.dom.readOnly = true;
							me.inputBox.addClass('c-fd-readonly');
						}
						else{
							me.input.dom.readOnly = false;
							me.inputBox.removeClass('c-fd-readonly');
						}
					}
					
					me.readOnly = readOnly;
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this,
						cls = me.cls;
					if(isA(cls)){
						cls.unshift('c-tfd');
					}
					else if(isS(cls)){
						cls = ['c-tfd', cls];
					}
					else{
						cls = 'c-tfd';
					}
					me.cls = cls;
					superClazz.initComponent.call(me);
				},
				/**
				 * @private
				 * @overwrite
				 */
				renderInput : function(box){
					var me = this;
					superClazz.renderInput.call(me, box);
					me.input = box.createChild({
						tag : 'input',
						atts : {
							placeholder : me.emptyText || '',
							type : 'password' === me.type ? 'password' : 'text',
							name : me.name || '',
							maxLength : me.maxLength || undefined
						}
					});
					if(me.readOnly){
						me.input.dom.readOnly = true;
						box.addClass('c-fd-readonly');
					}
					
					me.input.on('blur', me.onInputBlur, me);
					
					if(me.value){
						var v = me.value;
						delete me.value;
						me.setValue(v);
					}
				},
				/**
				 * @private
				 * 
				 */
				onInputBlur : function(){
					var me = this;
					me.fireEvent('changed', me.getValue(), me);
				},
				/**
				 * @private
				 * @overwrite
				 */
				doDisable : function(){
					var me = this;
					superClazz.doDisable.call(me);
					me.input.dom.disabled = true;
				},
				/**
				 * @private
				 * @overwrite
				 */
				doEnable : function(){
					var me = this;
					superClazz.doEnable.call(me);
					me.input.dom.disabled = false;
				}
			});
		}
	});
}());