/**
 * @class Cmp.form.ComboBox
 * @extend Cmp.form.Trigger
 * 
 * 
 * <p>
 * 
 * @cfg {Object} value ()
 * @cfg {String} emptyText ()
 * @cfg {String} label ()
 * @cfg {String} name ()
 * @cfg {Boolean} disabled ()truefalse
 * @cfg {Boolean} hideLabel ()truefalse
 * @cfg {String/Number} labelWidth (): '5rem'
 * @cfg {Boolean} readOnly true
 * @cfg {Array} options ()
 *		
 *			['','A','B','C'];
 *			[{value:'',text:''},{value:'A',text:'A'}{value:'B',text:'B'},{value:'C',text:'C'}]
 * </p>
 */
(function(){

	Cmp.define('Cmp.form.ComboBox',{
		extend : 'Cmp.form.Trigger',
		requires : [
			'Cmp.util.ArrayTableModel'
		],
		cls : true,
		factory : function(ext, reqs){
			var superClazz = ext.prototype,
				modelClazz = reqs[0];
			
			return Cmp.extend(ext, {
				/**
				 * @public
				 * 
				 * @param {Array} ops 
				 */
				setOptions : function(ops){
					var me = this;
					if(!me.input){
						me.options = ops;
						return ;
					}
						
					var	i = 0,
						len = isA(ops) ? ops.length : 0,
						o,v,t,
						vs = [],
						flag = false;
					for(;i<len;i++){
						o = ops[i];
						if(isO(o)){
							v = o.value;
							t = undefined === o.text ?  v : o.text;
						}
						else if(isS(o)){
							t = v = o;
						}
						
						if(undefined === v || null === v){
							continue;
						}
						if(v === me.value){
							flag = true;
						}
						vs.push([v,t]);
					}
					me.data.reload(vs);
					
					if(flag){
						//
					}
					else{
						//
						me.value = undefined;
						me.input.dom.value = '';
					}
					
					return me;
				},
				/**
				 * @overwrite
				 */
				setValue : function(value){
					var me = this;
					if(me.value === value){
						return ;
					}			
					me.value = value;
					if(me.data){
						var v = me.data.find(me.valueIndex, value);
						if(v && v.length > 0){
							v = v[0];
							v = v.get(me.textIndex);
							me.input.dom.value = v;
							
							
							var its = me.optionItems,
								i = 0, len = isA(its) ? its.length : 0,
								it,ix;
							for(;i<len;i++){
								it = its[i];
								if(it.value === value){
									it.active();
									ix = i;
								}
								else{
									it.unactive();
								}
							}
							if(ix > -1){
								me.activeOption = its[ix];
							}
							
						}
						else{
							if(me.activeOption){
								me.activeOption.unactive();
							}
							me.input.dom.value = '';
						}
					}
					me.fireEvent('changed', value, me);
				},	
				/**
				 * @overwrite
				 */
				getValue : function(){
					return this.value;
				},
				/**
				 * 
				 * @param {Object} value
				 */
				getTextByValue : function(value){
					var me = this;
					if(me.data){
						var r = me.data.getById(value);
						
						return r ? r.get(me.textIndex) : value;
					}
					return value;
				},
				/**
				 * @private
				 * 
				 */
				collect : function(){
					var me = this;
					if(me.extended){
						me.extended = false;
						var mask = me.getBodyMask();
						mask.hide();
						me.el.removeClass('c-cbx-extended');
						mask.un('click', me.collect, me);
						me.box.hide();
					}
				},	
				/**
				 * @private
				 * 
				 */
				extend : function(){
					var me = this;
					if(!me.extended){
						me.extended = true;
						var w = me.input.getWidth(),
							h = me.input.getHeight(),
							xy = me.input.getXY();
						me.box.setLeftTop(xy[0], xy[1]+h);	
						me.box.setWidth(w);
						me.box.show();
						me.el.addClass('c-cbx-extended');
						window.setTimeout(function(){
							var mask = me.getBodyMask();
							mask.show();
							mask.on('click', me.collect, me);
						}, 10);
					}
				},
				/**
				 * @private
				 * @overwrite
				 */
				doClickTrigger : function(){
					var me = this;
					if(me.disabled) {
						return;
					}
					if(me.extended){
						me.collect();
					}
					else{
						me.extend();
					}
				},
				/**
				 * @private
				 * @overwrite
				 */
				initComponent : function(){
					var me = this;
					var cls = ['c-cbx'];
					if(isS(me.cls)){
						cls.push(me.cls);
					}
					else if(isA(me.cls)){
						cls = cls.concat(me.cls);
					}
					me.cls = cls;
					me.readOnly = false !== me.readOnly;
					me.btnType = 'down';
					superClazz.initComponent.call(me);
					
					//
					if(!isO(me.data) || isA(me.options)){
						var i=0, 
							len = isA(me.options) ? me.options.length : 0, 
							o,v,t, 
							vs = [];
						for(;i<len;i++){
							o = me.options[i];
							if(isO(o)){
								v = o.value;
								t = o.text !== undefined ? o.text : v+'';
								vs.push([v,t]);
							}
							else{
								v = o,
								t = v ? v+'' : '';
								vs.push([v,t]);
							}
						}
						
						me.data = new modelClazz({
							keies : ['value','text'],
							idIndex : 0,
							values : vs
						});
						
						me.valueIndex = 'value';
						me.textIndex = 'text';
					}
					else{
						me.valueIndex = me.valueIndex || 'value';
						me.textIndex = me.textIndex || me.valueIndex;
					}
					
					if(me.data && isF(me.data.on)){
						me.data.on('changed', me.renderOptionItems, me);
					}
				},
				/**
				 * @private
				 * @overwrite
				 */
				renderInput : function(box){
					var me = this;
					superClazz.renderInput.call(me, box);
					me.getBodyMask();
					me.renderOptionView();
					me.renderOptionItems();
				},
				/**
				 * @private
				 * BODYbodyMask
				 */
				getBodyMask : function(){
					var me = this;
					if(!me.bodyMask){
						me.bodyMask = Cmp.getBody().createMask('ComboxMask', 900000);
					}
					return me.bodyMask;
				},
				/**
				 * @private
				 * 
				 */
				renderOptionView : function(){
					var me = this,
						cls = ['c-cbx-opbox'];
						
					if(me.styleCls){
						cls.push(me.styleCls);
					}
					if(me.sizeCls){
						cls.push(me.sizeCls);
					}
						
					me.box = Cmp.getBody().createChild({
						cls : cls
					});
					me.warp = me.box.createChild({
						cls : 'c-cbx-opwarp'
					});
					
					
					
					me.box.setHideModal('display');
					me.box.hide();
					me.box.on('click', me.collect, me);
				},
				/**
				 * @private
				 * 
				 */
				renderOptionItems : function(){
					var me = this;
					
					if(me.optionItems){
						//
						for(var i=0, len = me.optionItems.length; i<len; i++){
							me.optionItems[i].remove();
						}
					}
					
					var rs = me.data ? me.data.getRecords() : [],
						i=0, len = rs.length,o,
						items = [];
						
					for(;i<len;i++){
						o = me.renderOptionItem(me.warp, rs[i]);
						if(o){
							items.push(o);
						}
					}	
					
					me.optionItems = items;
				},
				/**
				 * @private
				 * 
				 */
				renderOptionItem : function(box, r){
					var me = this,
						el,rel,
						v = r.get(me.valueIndex),
						t = r.get(me.textIndex);
					el = box.createChild({
						cls : 'c-cbx-op-item',
						atts : {
							value : v,
							title : t
						},
						html : t
					});
					rel = {
						el : el,
						value : v,
						actived : false
					}
					
					
					rel.active = function(){
						if(!rel.actived){
							rel.el.addClass('c-cbx-op-item-active');
							rel.actived = true;
						}
						me.activeOption = rel;
					}
					rel.unactive = function(){
						if(rel.actived){
							rel.el.removeClass('c-cbx-op-item-active');
							rel.actived = false;
						}
						me.activeOption = false;
					}
					rel.remove = function(){
						if(rel.el){
							rel.el.remove();
						}
					}
					rel.clickItem = function(){
						if(me.activeOption){
							me.activeOption.unactive();
						}
						rel.active();
						me.setValue(rel.value);
					}
					
					rel.el.on('click', rel.clickItem, rel);
					
					if(me.value == v){
						rel.active();
					}
					
					return rel;
				},
				/**
				 * @private
				 * @overwrite
				 * 
				 */
				onInputBlur : Cmp.emptyFn
			});
		}
	
	});
}());