/**
 * 
 * 
 * @class Cmp.util.Color
 * @extend Object
 * 
 * @version 1.0.0
 * @since 2016-03-30
 * @author Jinhai 
 */
(function(){

	var VU = Cmp.util.ValueHelper;
	
	/**
	 * 
	 * @reutrn {Array} [r,g,b,a] false;
	 */
	var parse = function(s){
		s = s.toLowerCase();
		if(s.indexOf('#') === 0){
			return parseWeb(s);
		}
		else if(s.index('rgb(') === 0){
			return parseRgb(s);
		}
		else if(s.index('rgba(') === 0){
			return parseRgba(s);
		}
		
		return false;
	}
	/**
	 * '#'
	 * @reutrn {Array} [r,g,b,a] false;
	 */
	var parseWeb = function(s){
		s = s.substring(1);
		if(s.length !== 3 && s.length !== 6){
			return false;
		}
		
		var v = parseInt(s, 16),
			r,g,b;
		if(false === v){
			return v;
		}
		if(s.length === 3){
			r = (0xF00 & v) >> 8;
			r = (r << 4) + r;
			g = (0x0F0 & v) >> 4;
			g = (g << 4) + g;
			b = (0x00F & v);
			b = (b << 4) + b;
		}
		else {
			r = (0xFF0000 & v) >> 16;
			g = (0x00FF00 & v) >> 8;
			b = 0x0000FF & v;
		}
		
		return [r,g,b,100];
	}
	/**
	 * 'rgb('
	 * @reutrn {Array} [r,g,b,a] false;
	 */
	var parseRgb = function(s){
		s = s.substring(4);
		var ix = s.indexOf(')');
		if(ix > -1){
			s = s.substring(0, ix);
		}
		var vs = s.split(',');
		if(vs.length != 3){
			return false;
		}
		var r = VU.toInteger(vs[0], false),
			g = VU.toInteger(vs[1], false),
			b = VU.toInteger(vs[2], false);
		if(false === r || false === g || false === b){
			return false;
		}	
		r = 0xFF & r;
		g = 0xFF & g;
		b = 0xFF & b;
		return [r,g,b,100];
	}
	/**
	 * 'rgba('
	 * @reutrn {Array} [r,g,b,a] false;
	 */
	var parseRgba = function(s){
		s = s.substring(5);
		var ix = s.indexOf(')');
		if(ix > -1){
			s = s.substring(0, ix);
		}
		var vs = s.split(',');
		if(vs.length != 4){
			return false;
		}
		var r = VU.toInteger(vs[0], false),
			g = VU.toInteger(vs[1], false),
			b = VU.toInteger(vs[2], false),
			a = VU.toFloat(vs[3], false);
		if(false === r || false === g || false === b 
			|| false === a){
			return false;
		}	
		r = 0xFF & r;
		g = 0xFF & g;
		b = 0xFF & b;
		a = Math.round(a*100) % 101;
		
		return [r,g,b,a];
	}
	/**
	 * 
	 * @param {String/Number} r #808080
	 *						0~255;
	 * @param {Number} g r0~255;128
	 * @param {Number} b r0~255;128
	 * @param {Number} a 01000~100100
	 */
	var Color = function(r, g, b, a){
		var me = this,
			red = 128,
			greed = 128,
			blue = 128,
			alpha = 100;
		
		/**
		 * 
		 * @return {Number} 0~255
		 */
		me.getRed = function(){
			return red;
		}
		/**
		 * 
		 * @return {Number} 0~255
		 */
		me.getGreen = function(){
			return greed;
		}
		/**
		 * 
		 * @return {Number} 0~255
		 */
		me.getBlue = function(){
			return blue;
		}
		/**
		 * 
		 *@return {Number} 0~1000100
		 */
		me.getAlpha = function(){
			return alpha;
		}
		
			
		if(isS(r)){
			var vs = parse(r);
			if(vs){
				red = vs[0];
				greed = vs[1];
				blue = vs[2];
				alpha = vs[3];
			}
		}
		else if(isN(r)){
			red = r % 256;
			greed = isN(g) ? g % 256 : 128;
			blue = isN(b) ? b % 256 : 128;
			alpha = isN(a) ? a % 101 : 100;
		}
		else{
			//
		}
	}
	
	Color.prototype = {
		/**
		 * @overwrite
		 */
		valueOf : function(){
			var me = this,
				a = me.getAlpha();
			var v = me.getRed() << 16 + me.getGreen() << 8 + me.getBlue();
			if(a === 100){
				v = v + (255 << 24);
			}
			else if(a !== 0){
				a = Math.floor(a / 100 * 255);
				v = v + (a << 24);
			}
			return v;
		},
		/**
		 * '#'Web
		 * @return {String} '#'
		 */
		getWebValue : function(){
			var me = this,
				r = me.getRed(),
				g = me.getGreen(),
				b = me.getBlue(),
				rel = ['#'];
			rel.push(r.toString(16));
			rel.push(g.toString(16));
			rel.push(b.toString(16));
			return rel.join('');	
		},
		/**
		 * 'rgb(r,g,b)'
		 */
		getRgbValue : function(){
			var me = this,
				r = me.getRed(),
				g = me.getGreen(),
				b = me.getBlue(),
				rel = ['rgb('];
			rel.push(r);
			rel.push(',');
			rel.push(g);
			rel.push(',');
			rel.push(b);
			rel.push(')');
			return rel.join('');	
		},
		/**
		 * 'rgba(r,g,b,a)'
		 */
		getRgbaValue : function(){
			var me = this,
				r = me.getRed(),
				g = me.getGreen(),
				b = me.getBlue(),
				a = me.getAlpha(),
				rel = ['rgba('];
			rel.push(r);
			rel.push(',');
			rel.push(g);
			rel.push(',');
			rel.push(b);
			rel.push(',');
			rel.push(a / 100);
			rel.push(')');
			return rel.join('');	
		},
		/**
		 * Color;
		 * @param {Number} alpha 01000100100
		 * @return {Color} Color
		 */
		replaceAlpha : function(alpha){
			var me = this;
			return new PK.Color(me.getRed(), me.getGreen(), me.getBlue(), alpha);
		},
		/**
		 * 
		 * @param {Color} color ()null
		 * @param {Number} offset ()010050
		 *		0this10050
		 * @return {Color} 
		 */
		mingle : function(color, offset){
			if(!color || isF(color.getRed)){
				return null;
			}
			offset = isN(offset) ? offset %101 : 50;
			var me = this;
			if(0 === offset){
				return new PK.Color(me.getRed(), me.getGreen(), me.getBlue(), me.getAlpha());
			}
			if(100 === offset){
				return new PK.Color(color.getRed(), color.getGreen(), color.getBlue(), color.getAlpha());
			}
			var mc = (100 - offset) / 100,
				cc = offset / 100;
			
			var r = Math.round(me.getRed()*mc + color.getRed()*cc),
				g = Math.round(me.getGreen()*mc + color.getGreen()*cc),
				b = Math.round(me.getBlue()*mc + color.getBlue()*cc),
				a = me.getAlpha()*mc + color.getAlpha()*cc;
			
			return new Color(r,g,b,a);	
		},
		/**
		 * @overwrite
		 */
		toString : function(){
			var me = this,
				a = me.getAlpha();
			if(100 === a){
				return me.getWebValue();
			}
			else{
				return me.getRgbaValue();
			}	
		}
	};
	
	var PRO = Color.prototype;
	/**
	 * '#'Web
	 * @see #getWebValue
	 */
	PRO.web = PRO.getWebValue;
	/**
	 * 'rgb(r,g,b)'
	 */
	PRO.rgb = PRO.getRgbValue;
	/**
	 * 'rgba(r,g,b,a)'
	 */
	PRO.rgba = PRO.getRgbaValue;
	
	/**
	 * @public
	 * @static
	 * Colorundefined;
	 */
	Color.parse = function(s){
		var vs = parse(s);
		if(vs){
			return new PK.Color(vs[0],vs[1],vs[2],vs[3]);
		}
		return undefined;
	}
	Cmp.define('Cmp.util.Color',{
		factory : function(){
			return Color;
		}
	});
}());